<template>
  <HomeFrame :config="theFrameConfig" @action="handleAction">
    <div class="pager-box">
      <el-pagination v-model:current-page="currentPage" v-model:page-size="pageSize"
        :page-sizes="[5, 10, 20, 50, 100, 200, 500]" :background="background" layout="total, sizes, prev, pager, next"
        :total="theCountItemsTotal" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
    </div>
    <MyRepoList :data="theItems" :expandedItemId="expandedItemId" @action="handleAction" />
    <MyOpenItemDialog v-model="displayOpenItemDialog" :data="dataForOpenItemDialog" />
    <FastImportDialog v-model="displayFastImportDialog" />

    <OpenWithDrawer v-model="displayOpenWithDrawer" />
  </HomeFrame>
</template>

<script>
import MyRepoList from "./item-list.vue";
import MyOpenItemDialog from "./open-item-dialog.vue";
import FastImportDialog from "@/components/import-fast/fast-import-dialog.vue";
import OpenWithDrawer from "@/components/open-with/open-with-drawer.vue";

export default {
  name: "home-local-repository-index",

  components: {
    MyRepoList,
    MyOpenItemDialog,
    FastImportDialog,
    OpenWithDrawer,
  },

  computed: {
    theFrameConfig() {
      return {
        pager: false,
        home: true,
        nav: true,
        search: true,
        searchCategory: "",
      };
    },

    theCountItemsTotal() {
      return this.$store.getters["local_repository/total"];
    },

    theItems() {
      return this.$store.getters["local_repository/currentPageItems"];
    },
  },

  data() {
    return {
      pageSize: 1,
      currentPage: 1,
      expandedItemId: 0,
      background: "",
      displayFastImportDialog: false,
      displayOpenItemDialog: false,
      displayOpenWithDrawer: false,
      dataForOpenItemDialog: {},
    };
  },

  methods: {
    handleAction(p) {
      let name = p.name;
      if (name == "refresh") {
        this.fetch();
      } else if (name == "add") {
        this.add();
      } else if (name == "applyFilter") {
        this.applyFilter(p.value);
      } else if (name == "open-item-with") {
        this.openItemWith(p);
      } else if (name == "expand-item") {
        this.makeItemExpanded(p);
      } else if (name == "fold-item") {
        this.makeItemFolded(p);
      } else if (name == 'intent') {
        this.handleIntent(p)
      } else {
        console.warn("unsupported action: " + name);
      }
    },

    fetch() {
      this.$store.dispatch("local_repository/fetch");
    },

    initOpenWithList() {
      this.$store.dispatch("executable/init");
    },

    add() {
      // this.$router.push("/import-local-repo");
      this.displayFastImportDialog = true;
    },

    handleIntent(e) {
      let intent = e.intent;
      let vo = { intents: [intent] }
      this.$store.dispatch('intent/start', vo)
    },

    handleSizeChange(value) {
      let size = value;
      this.$store.commit("local_repository/update", { size });
    },

    handleCurrentChange(value) {
      let index = value - 1;
      this.$store.commit("local_repository/update", { index });
    },

    applyFilter(filter) {
      this.$store.commit("local_repository/update", { filter });
    },

    pageViewToModel() {
      let index = this.currentPage - 1;
      let size = this.pageSize;
      this.$store.commit("local_repository/update", { index, size });
    },

    pageModelToView() {
      this.currentPage = this.$store.getters["local_repository/currentPage"];
      this.pageSize = this.$store.getters["local_repository/pageSize"];
    },

    makeItemExpanded(e) {
      let item = e.item;
      if (item == null) {
        return;
      }
      let id = item.id;
      this.expandedItemId = id;
      this.$store.dispatch("local_repository/fetchById", { id });
    },

    makeItemFolded() {
      this.expandedItemId = 0;
      // this.$store.dispatch("local_repository/fetchById", { id });
    },

    openItemWith(e) {
      let item = e.item;
      this.dataForOpenItemDialog = item;
      this.displayOpenWithDrawer = true;
    },

    openFastImportDialog() {
      this.displayFastImportDialog = true;
    },
  },

  mounted() {
    this.pageModelToView();
    this.initOpenWithList();
    this.fetch();
  },
};
</script>

<style scoped>
.pager-box {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 50px;
}
</style>

