
import StateJS from '@/js/state.js'


const use_root = { root: true }

////////////////////////////////////////////////////////////////////////////////

function stringifyIDsFromItems(items) {
    let dst = ""
    for (var i in items) {
        let id = items[i].id;
        dst += ("." + id)
    }
    return dst;
}

function makeDuplicateName(rawValue) {
    let now = "-" + (new Date()).getTime()
    let tag = 'duplicate'
    let i = ("" + rawValue).indexOf(tag)
    if (i < 0) {
        tag = "-" + tag
    } else {
        tag = ""
    }
    return rawValue + tag + now
}



////////////////////////////////////////////////////////////////////////////////

function Filter() { }

Filter.prototype = {

    prepare(text) {
        Filter.text = text;
    },

    accept(item) {
        let want = "" + Filter.text;
        let have = "" + this.makeSearchText(item);
        let i = have.indexOf(want.trim().toLowerCase())
        return i >= 0
    },

    makeSearchText(item) {
        let builder = ""
        let list = [
            item.name,
            item.group,
            item.title,
        ]
        for (var i in list) {
            let item = list[i];
            if (item == null) {
                continue
            }
            builder += (item + "\n");
        }
        return builder.toLowerCase()
    },

}

////////////////////////////////////////////////////////////////////////////////

const state = {
    templates: [],
    macro_properties: [],
};

const filterObj = new Filter();
const state_rw = StateJS.createStateRW({ pk: "id", listname: "intent_templates" })
state_rw.init(state)
state_rw.setFilter(state, { text: "", filter: filterObj })
state_rw.setPage(state, { limit: 10, offset: 0 })



const getters = {

    all(state) {
        return state_rw.listAll(state)
    },

    list(state) {
        return state_rw.listCurrentPage(state)
    },

    macro_properties(state) {
        return state.macro_properties
    },

    current_page(state) {
        return state_rw.currentPageNumber(state)
    },

    page_size(state) {
        return state_rw.limit(state)
    },

    total(state) {
        return state_rw.total(state)
    },

    filter(state) {
        return state_rw.getFilterText(state)
    },

}

const mutations = {
    handle_fetch_resp_vo(state, vo) {
        state_rw.updateVO(state, vo)
    },

    handle_insert_resp_vo(state, vo) {
        state_rw.updateVO(state, vo)
    },

    handle_update_resp_vo(state, vo) {
        state_rw.updateVO(state, vo)
    },

    handle_remove_id(state, id) {
        state_rw.remove(state, id)
    },

    update_macro_properties_with_vo(state, vo) {
        let src = vo.macro_properties; // as a map[string]string
        let dst = [];
        for (var name in src) {
            let desc = src[name]
            let item = {}
            item.id = name;
            item.name = name;
            item.label = name;
            item.description = desc;
            dst.push(item)
        }
        dst.sort((a, b) => {
            let str1 = a.name + ""
            let str2 = b.name + ""
            return str1.localeCompare(str2)
        })
        state.macro_properties = dst;
    },

    update(state, p) {
        let vo = p.vo;
        let limit = p.size;
        let index = p.index;
        let text = p.filter;

        if (vo != null) {
            state_rw.updateVO(state, vo)
        }

        if (limit != null || index != null) {
            state_rw.setPage(state, { limit, index })
        }

        if (text != null) {
            state_rw.setFilter(state, { text })
        }
    },

    reset(state) {
        state_rw.reset(state)
    },

};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = 'intent-templates'
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('reset')
                context.commit('handle_fetch_resp_vo', vo)
                return Promise.resolve(r)
            })
    },

    insert(context, p) {
        let item = p.item;
        let method = 'POST'
        let type = 'intent-templates'
        let data = {
            intent_templates: [item]
        }
        return context.dispatch("rest/execute", { method, type, data }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_insert_resp_vo', vo)
                return Promise.resolve(r)
            })
    },

    update(context, p) {
        let item = p.item;
        let id = item.id;
        let method = 'PUT'
        let type = 'intent-templates'
        let data = {
            intent_templates: [item]
        }
        if (id == null) { return }
        if (id < 1) { return }
        return context.dispatch("rest/execute", { method, type, id, data }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_update_resp_vo', vo)
                return Promise.resolve(r)
            })
    },

    remove(context, p1) {

        let id = p1.id;
        let item = p1.item;
        let items = p1.items;
        let params = {}

        if (id != null) {
            // NOP
        } else if (items != null) {
            params.ids = stringifyIDsFromItems(items)
        } else if (item != null) {
            id = item.id
        } else {
            return Promise.reject("no param:id|item|items")
        }

        let method = 'DELETE'
        let type = 'intent-templates'
        let p2 = { method, type, id, params }
        return context.dispatch("rest/execute", p2, use_root)
            .then((r) => {
                if (id != null) {
                    context.commit('handle_remove_id', id)
                } else if (items != null) {
                    context.dispatch('fetch')
                }
                return Promise.resolve(r)
            })
    },

    import_preset_templates(context) {
        let method = 'POST'
        let type = 'intent-templates/import-preset'
        let data = {}
        return context.dispatch("rest/execute", { method, type, data }, use_root)
            .then((r) => {
                // let vo = r.data;
                // context.commit('handle_remove_id', id)
                return Promise.resolve(r)
            })
    },

    fetch_macro_props(context) {
        let method = 'GET'
        let type = 'intent-templates'
        let id = "macro/properties"
        return context.dispatch("rest/execute", { method, type, id }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('update_macro_properties_with_vo', vo)
                return Promise.resolve(r)
            })
    },

    duplicate(context, p) {
        let j = JSON.stringify(p.item)
        let item = JSON.parse(j)
        item.method = makeDuplicateName(item.method)
        item.name = makeDuplicateName(item.name)
        return context.dispatch('insert', { item })
    },

};


////////////////////////////////////////////////////////////////////////////////

export default {
    name: "module_intent_template",
    namespaced: true,
    state, mutations, actions, getters
}
