

function RouteJS() {
}

RouteJS.prototype = {

    GetRepositoryID(context) {
        return context.$route.params["repository_id"]
    },

    GetCommitID(context) {
        return context.$route.params["commit_id"]
    },

    GetTagID(context) {
        return context.$route.params["tag_id"]
    },

}


function NewRouteJS() {
    return new RouteJS()
}


export default {
    name: "route-js",
    NewRouteJS,
}
