

const use_root = { root: true }


////////////////////////////////////////////////////////////////////////////////


function LoadingState(action, params) {
    this.action = action;
    this.params = params;
    this.result = null;
    this.loading = false;
}

LoadingState.prototype = {

    loaded() {
        let r = this.result;
        return (r != null)
    },

    cached() {
        let r = this.result;
        return Promise.resolve(r)
    },

    load(context) {

        if (this.loading) {
            let r = {}
            return Promise.resolve(r)
        }
        this.loading = true;

        let action = this.action;
        let params = this.params;
        let self = this;

        return context.dispatch(action, params, use_root).then((r) => {
            self.handle(r)
            return Promise.resolve(r)
        })
    },

    handle(r) {
        this.result = r;
    },

}


////////////////////////////////////////////////////////////////////////////////

function LoadingStateManager() {
    this.cache = {};
}

LoadingStateManager.prototype = {
    get(action, p) {
        let s = this.cache[action]
        if (s == null) {
            s = new LoadingState(action, p)
            this.cache[action] = s;
        }
        return s;
    }
}

const lsm = new LoadingStateManager();

////////////////////////////////////////////////////////////////////////////////


const state = {
};

const getters = {
}

const mutations = {
};

const actions = {
    load_executables(context) {
        let s = lsm.get('executable/fetch', {});
        if (s.loaded()) {
            return s.cached()
        }
        return s.load(context)
    },

    load_mediae(context) {
        let s = lsm.get('media/fetch', {});
        if (s.loaded()) {
            return s.cached()
        }
        return s.load(context)
    },
};


export default {
    name: "module_lazy",
    namespaced: true,
    state, mutations, actions, getters
}
