

const use_root = { root: true }

function nop() { }

////////////////////////////////////////////////////////////////////////////////

const state = {
    want: {}, // the want-repo
    have: {}, // the have-repo
};



const getters = {
    info(state) {
        return state.have
    }
}

const mutations = {
    handle_vo(state, vo) {
        if (vo == null) { return }
        let all = vo.local_repositories;
        if (all == null) { return }
        let r0 = all[0]
        if (r0 == null) { return }
        state.have = r0
    },
};

const actions = {
    fetch(context, p) {

        // let path = p.path
        nop(p)

        if (p == null) {
            p = {}
        }


        let id = p.id;
        let state = context.state;

        if (id == null) {
            id = state.want.id
        }

        let method = 'GET'
        let type = 'local-repositories'

        // context.commit('reset')
        state.want.id = id;

        return context.dispatch("rest/execute", { method, type, id }, use_root).then((r) => {
            let vo = r.data;
            context.commit('handle_vo', vo)
        })
    },

};


export default {
    name: "module_current_repository",
    namespaced: true,
    state, mutations, actions, getters
}
