
import StateJS from '@/js/state.js'


const use_root = { root: true }
const this_module_api_type = "software-sets"


////////////////////////////////////////////////////////////////////////////////

function Filter() { }

Filter.prototype = {}

////////////////////////////////////////////////////////////////////////////////


function ListItemPreProcesser() { }

ListItemPreProcesser.prototype = {

    compare(a, b) {
        let n1 = "" + a.name
        let n2 = "" + b.name
        return n1.localeCompare(n2);
    },

}


////////////////////////////////////////////////////////////////////////////////

const state = {
    items: []
};

const filter_obj = new Filter();
const state_rw = StateJS.createStateRW({ pk: "id", listname: "sets" })
state_rw.init(state)
state_rw.setFilter(state, { filter: filter_obj })
state_rw.setPage(state, { limit: 10, offset: 0 })


const getters = {
    list(state) {
        return state_rw.listCurrentPage(state)
    },

    all(state) {
        return state_rw.listAll(state)
    },

    total(state) {
        return state_rw.total(state)
    },

    current_page(state) {
        return state_rw.currentPageNumber(state)
    },

    page_size(state) {
        return state_rw.limit(state)
    },
}

const mutations = {

    reset(state) {
        state_rw.reset(state, null)
    },

    handle_fetch_resp_vo(state, vo) {
        let list = vo.sets
        let lipp = new ListItemPreProcesser();
        list.sort((a, b) => { return lipp.compare(a, b) })
        vo.sets = list;
        state_rw.updateVO(state, vo)
    },

    update(state, p) {
        let vo = p.vo;
        let limit = p.size;
        let index = p.index;
        let text = p.filter;

        if (vo != null) {
            state_rw.updateVO(state, vo)
        }

        if (limit != null || index != null) {
            state_rw.setPage(state, { limit, index })
        }

        if (text != null) {
            state_rw.setFilter(state, { text })
        }
    }
};


const actions = {
    fetch(context) {
        let method = 'GET'
        let type = this_module_api_type
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('reset')
                context.commit('handle_fetch_resp_vo', vo)
            })
    },

    fetch_one(context, p) {
        let id = p.id
        let item = p.item
        if (id == null) {
            if (item == null) { return }
            id = item.id
        }
        let method = 'GET'
        let type = this_module_api_type
        return context.dispatch("rest/execute", { method, type, id }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_fetch_resp_vo', vo)
            })
    },


    // update_all(context) {
    //     let method = 'POST'
    //     let type = this_module_api_type
    //     let id = "update"
    //     let data = {}
    //     return context.dispatch("rest/execute", { method, type, id, data }, use_root)
    //         .then((r) => {
    //             let vo = r.data;
    //             context.commit('handle_update_resp_vo', vo)
    //         })
    // },

    install(context, { item }) {
        let method = 'POST'
        let type = this_module_api_type
        let id = "install"
        let data = { sets: [item] }
        return context.dispatch("rest/execute", { method, type, id, data }, use_root)
            .then((r) => {
                // let vo = r.data;
                // context.commit('handle_update_resp_vo', vo) 
                context.dispatch('fetch_one', { item })
                return Promise.resolve(r)
            })
    },

    reinstall(context, { item }) {
        let method = 'POST'
        let type = this_module_api_type
        let id = "re-install"
        let data = { sets: [item] }
        return context.dispatch("rest/execute", { method, type, id, data }, use_root)
            .then((r) => {
                // let vo = r.data;
                // context.commit('handle_update_resp_vo', vo) 
                context.dispatch('fetch_one', { item })
                return Promise.resolve(r)
            })
    },

    uninstall(context, { item }) {
        let method = 'POST'
        let type = this_module_api_type
        let id = "uninstall"
        let data = { sets: [item] }
        return context.dispatch("rest/execute", { method, type, id, data }, use_root)
            .then((r) => {
                // let vo = r.data;
                // context.commit('handle_update_resp_vo', vo) 
                context.dispatch('fetch_one', { item })
                return Promise.resolve(r)
            })
    },

    upgrade(context, { item }) {
        let method = 'POST'
        let type = this_module_api_type
        let id = "upgrade"
        let data = { sets: [item] }
        return context.dispatch("rest/execute", { method, type, id, data }, use_root)
            .then((r) => {
                // let vo = r.data;
                // context.commit('handle_update_resp_vo', vo) 
                context.dispatch('fetch_one', { item })
                return Promise.resolve(r)
            })
    },

};

////////////////////////////////////////////////////////////////////////////////

export default {
    name: "module_plugin_set",
    namespaced: true,
    state, mutations, actions, getters
}
