<template>
  <el-dialog title="">
    <el-form label-width="48%">
      <el-form-item label=""> {{ latest.version }} </el-form-item>
      <el-form-item label=""> {{ latest.full_name }}</el-form-item>
      <el-form-item label=""> {{ latest.size }}</el-form-item>
      <el-form-item label=""> {{ latest.release_at }} </el-form-item>
    </el-form>
    <div class="dialog-toolbar">
      <el-button type="primary" @click="openWebPage">  </el-button>
      <el-button @click="ignoreThisVersion" v-show="displayIgnoreButton">
        
      </el-button>
      <!-- <el-button>  </el-button> -->
    </div>
  </el-dialog>
</template> 

<script>
export default {
  name: "check-update-dialog.vue",

  computed: {
    latest() {
      return this.$store.getters["checkupdate/latest"];
    },

    ignore() {
      return this.$store.getters["checkupdate/ignore"];
    },

    displayIgnoreButton() {
      let ignored = this.$store.getters["checkupdate/has_ignored"];
      return !ignored;
    },
  },

  methods: {
    openWebPage() {
      let url = this.latest.web_page_url;
      window.open(url, "_blank");
    },

    ignoreThisVersion() {
      let ignore = this.latest;
      let vo = {
        checkupdate: { ignore },
      };
      this.$store.dispatch("checkupdate/ignore", vo).then(() => {
        this.close();
      });
    },

    close() {
      this.$emit("update:modelValue", false);
    },
  },
};
</script>

<style scoped>
.dialog-toolbar {
  margin-top: 30px;
  text-align: center;
}
</style>
