<template>
  <div>
    <!-- path -->
    <h3 class="sub-list-title"></h3>
    <div class="text-path">{{ thePath }}</div>

    <!-- projects -->
    <h3 class="sub-list-title" v-show="projects.length > 0"></h3>
    <div class="projects sub-list">
      <div class="projects-item sub-list-item" v-for="item in projects" :key="item.name" :title="item.path"
        @click="handleClickProjectItem(item)">
        <div class="sub-list-item-outer" :style="computeItemOuterStyle(item)">
          <div class=" sub-list-item-inner"> {{ item.name }} </div>
        </div>
      </div>
    </div>

    <!-- worktrees -->
    <h3 class="sub-list-title" v-show="worktrees.length > 0"></h3>
    <div class="worktrees sub-list">
      <div class="worktrees-item sub-list-item" v-for="item in worktrees" :key="item.name" :title="item.path"
        @click="handleClickWorktreeItem(item)">
        <div class="sub-list-item-outer" :style="computeItemOuterStyle(item)">
          <div class=" sub-list-item-inner"> {{ formatWorktreeName(item) }} </div>
        </div>
      </div>
    </div>

    <!-- submodules -->
    <h3 class="sub-list-title" v-show="submodules.length > 0"></h3>
    <div class="submodules sub-list">
      <div class="submodules-item sub-list-item" v-for="item in submodules" :key="item.name" :title="item.path"
        @click="handleClickSubmoduleItem(item)">
        <div class="sub-list-item-outer" :style="computeItemOuterStyle(item)">
          <div class=" sub-list-item-inner"> {{ item.name }} </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GetterJS from "@/js/getter";

export default {
  name: "home-local-repository-detail",

  computed: {
    theIcon() {
      return this.GetByPath("icon", "/bad/icon/path");
    },

    theTitle() {
      return this.GetByPath("name", "");
    },

    theDescription() {
      return this.GetByPath("description", "");
    },

    thePath() {
      return this.GetByPath("path", "");
    },

    projects() {
      return this.GetByPath("projects", []);
    },

    submodules() {
      return this.GetByPath("submodules", []);
    },

    worktrees() {
      return this.GetByPath("worktrees", []);
    },
  },

  data() {
    return {
      currentPath: "",
    }
  },

  methods: {
    GetByPath(path, def) {
      return GetterJS.Get(this.data, path, def);
    },

    formatWorktreeName(item) {
      let name = item.name;
      if (name == "") {
        name = "[]";
      }
      return name;
    },

    handleClickProjectItem(item) {
      let intent = { project: item };
      let name = 'intent'
      let e = { name, intent }
      this.setCurrentPath(item.path)
      this.$emit('action', e)
    },

    handleClickSubmoduleItem(item) {
      let intent = { submodule: item };
      let name = 'intent'
      let e = { name, intent }
      this.setCurrentPath(item.path)
      this.$emit('action', e)
    },

    handleClickWorktreeItem(item) {
      let intent = { worktree: item };
      let name = 'intent'
      let e = { name, intent }
      this.setCurrentPath(item.path)
      this.$emit('action', e)
    },

    computeItemOuterStyle(item) {
      let p1 = item.path;
      let p2 = this.currentPath
      let p = {}
      if (p1 == p2) {
        p.borderColor = "#606060"
      }
      return p
    },

    setCurrentPath(path) {
      this.currentPath = path
    }
  },

  props: {
    data: Object,
  },
};
</script>

