

const use_root = { root: true }


////////////////////////////////////////////////////////////////////////////////

function getWithoutNull(o) {
    if (o == null) {
        o = {}
    }
    return o
}

////////////////////////////////////////////////////////////////////////////////

const state = {
    current: {},
    ignore: {},
    latest: {},
    has_new_version: false,
    has_ignored: false,
};

const getters = {

    current(state) {
        return state.current
    },

    ignore(state) {
        return state.ignore
    },

    latest(state) {
        return state.latest
    },

    has_ignored(state) {
        return state.has_ignored
    },

    has_new_version(state) {
        return state.has_new_version
    },

}

const mutations = {
    handle_vo(state, vo) {
        vo = getWithoutNull(vo)
        let cu = getWithoutNull(vo.checkupdate)
        state.current = getWithoutNull(cu.current)
        state.latest = getWithoutNull(cu.latest)
        state.ignore = getWithoutNull(cu.ignore)
        state.has_ignored = (cu.has_ignored)
        state.has_new_version = (cu.has_new_version)
    }
};

const actions = {
    check(context, vo1) {
        let action = 'fetch'
        let method = 'POST'
        let type = 'about/checkupdate'
        let data = vo1;
        return context.dispatch("rest/execute", { method, type, data, action }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_vo', vo)
                return Promise.resolve(r)
            })
    },

    ignore(context, vo1) {
        let method = 'POST'
        let type = 'about/ignore'
        let data = vo1;
        return context.dispatch("rest/execute", { method, type, data }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_vo', vo)
            })
    },
};


export default {
    name: "module_check_update",
    namespaced: true,
    state, mutations, actions, getters
}
