
import FileBrowserJS from './../js/file_browser.js'

const use_root = { root: true }
const state_rw = FileBrowserJS.CreateStateRW()



const state = {
    items: []
};
state_rw.InitWithBanks(state, ["dialog_open", "dialog_save", "default"])


const getters = {

    GetSelf: (state) => (bank) => {
        return state_rw.GetBank(state, bank).GetSelf()
    },

    GetItems: (state) => (bank) => {
        return state_rw.GetBank(state, bank).GetItems()
    },

    GetPathString: (state) => (bank) => {
        return state_rw.GetBank(state, bank).GetPathString()
    },

    GetPathElements: (state) => (bank) => {
        return state_rw.GetBank(state, bank).GetPathElements()
    },

}

const mutations = {

    init(state, p) {
        let bank = p.bank;
        state_rw.GetBank(state, bank).Init(p)
    },

    handle_vo(state, p) {
        let bank = p.bank;
        let vo = p.vo;
        state_rw.GetBank(state, bank).UpdateVO(vo)
    }

};

const actions = {
    fetch(context, p) {
        let bank = p.bank;
        let path = p.path;
        let base = p.base;
        let href = p.href;

        let state = context.state;
        let pbank = state_rw.GetBank(state, bank)

        if (base == null) {
            base = pbank.GetBaseURL();
        }
        if (path == null) {
            path = pbank.GetPathString();
        }

        if (href != null) {
            path += "/" + href;
        }

        let action = 'fetch'
        let method = 'POST'
        let type = 'file-query'
        let data = {
            path, base
        }
        return context.dispatch("rest/execute", { action, method, type, data }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_vo', { bank, vo })
            })
    },
};


export default {
    name: "module_file_browser",
    namespaced: true,
    state, mutations, actions, getters
}
