<template>
  <el-drawer class="open-with-drawer" :title="theDrawerTitle" :direction="direction" :before-close="handleClose" style="
      background-color: rgb(230, 230, 230);
      max-height: 380px;
      min-height: 300px;
    ">
    <span>
      <MyPanel />
    </span>
  </el-drawer>
</template>

<script>
import MyPanel from "./open-with-panel.vue";
import Intent from '@/js/intent.js'

export default {
  name: "open-with-drawer",

  components: { MyPanel },

  computed: {

    current() {
      let cur = this.$store.getters["intent/current"];
      if (cur == null) {
        cur = {};
      }
      return cur;
    },

    revision() {
      return this.$store.getters["intent/revision"];
    },

    theTargetName() {
      return this.$store.getters["intent/name"];
    },

    theTargetType() {
      return this.$store.getters["intent/type"];
    },

    theTargetTypeText() {
      return this.$store.getters["intent/typetext"];
    },

    theDrawerTitle() {
      let name = this.theTargetName;
      let t = this.theTargetType;
      let tt = this.theTargetTypeText;
      if (name == "" && t == "worktree") {
        name = "[]"
      }
      return tt + ": " + name;
    },
  },

  data() {
    return {
      direction: "btt", // bottom to top
    };
  },

  methods: {
    handleClose(done) {
      done();
    },
  },
};
</script>

<style scoped>
.open-with-drawer {
  background-color: rgb(190, 190, 190);
  max-height: 300px;
}
</style>
