
// import StateJS from './../js/state.js'


const use_root = { root: true }
const this_module_api_type = "statistics"


////////////////////////////////////////////////////////////////////////////////


// Backups         int64 `json:"backups"`
// ContentTypes    int64 `json:"content_types"`
// Executables     int64 `json:"executables"`
// IntentTemplates int64 `json:"intent_templates"`
// Mediae          int64 `json:"mediae"`
// PlugIns         int64 `json:"plug_ins"`
// Projects        int64 `json:"projects"`
// Repositories    int64 `json:"repositories"`


const state = {
    backups: 0,
    content_types: 0,
    executables: 0,
    intent_templates: 0,
    mediae: 0,
    plug_ins: 0,
    projects: 0,
    repositories: 0,
};

const getters = {
    backups(state) { return state.backups },

    content_types(state) { return state.content_types },

    executables(state) { return state.executables },

    intent_templates(state) { return state.intent_templates },

    mediae(state) { return state.mediae },

    plug_ins(state) { return state.plug_ins },

    projects(state) { return state.projects },

    repositories(state) { return state.repositories },
}

const mutations = {
    handle_fetch_resp_vo(state, vo) {
        let src = vo.statistics
        let dst = state
        for (var key in src) {
            let item = src[key]
            dst[key] = item
        }
    },
};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = this_module_api_type
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_fetch_resp_vo', vo)
                return Promise.resolve(r)
            })
    },
};

////////////////////////////////////////////////////////////////////////////////

export default {
    name: "module_statistic",
    namespaced: true,
    state, mutations, actions, getters
}
