<template>
  <div class="overview">
    <div class="overview-left">
      <div class="title" :title="thePath">{{ theTitle }}</div>
      <div class="description">{{ theDescription }}</div>
      <div class="toolbar-for-overview">
        <el-button class="btn-expand-fold" @click="handleClickItemExpand" title="" circle size="small"
          v-show="!expanded">
          <el-icon>
            <Plus />
          </el-icon>
        </el-button>
        <el-button class="btn-expand-fold" @click="handleClickItemFold" title="" circle size="small" v-show="expanded">
          <el-icon>
            <Minus />
          </el-icon>
        </el-button>
        <el-button circle size="small" v-show="false">3</el-button>
        <el-button circle size="small" v-show="false">4</el-button>
        <el-button circle size="small" v-show="false">5</el-button>
      </div>
    </div>

    <div class="overview-mid"></div>

    <div class="overview-right">
      <MyExeList :repository="data" @action="handleAction" />
    </div>
  </div>
</template>

<script setup>
import { Plus, Minus } from "@element-plus/icons-vue";
</script>

<script>
import MyExeList from "./open-with-list.vue";

export default {
  name: "home-local-repository-item",

  components: { MyExeList },

  computed: {
    theIcon() {
      // return this.getDataWithoutNull().icon;
      return "/image/icon_empty.png";
    },

    theTitle() {
      return this.getDataWithoutNull().name;
    },

    thePath() {
      return this.getDataWithoutNull().path;
    },

    theDescription() {
      return this.getDataWithoutNull().description;
    },
  },

  methods: {
    getDataWithoutNull() {
      let d = this.data;
      if (d == null) {
        d = {};
      }
      return d;
    },

    handleClickItemFold() {
      let name = "fold-item";
      let item = this.data;
      this.$emit("action", { name, item });
    },

    handleClickItemExpand() {
      let name = "expand-item";
      let item = this.data;
      this.$emit("action", { name, item });
    },

    handleAction(e) {
      this.$emit('action', e)
    }
  },

  props: {
    data: Object,
    expanded: Boolean,
  },
};
</script>

