
const use_root = { root: true }


////////////////////////////////////////////////////////////////////////////////


function updateStateForItems(src, table, dst) {

    let i = 0;

    for (i in src) {
        let item = src[i]
        let id = item.id;
        table[id] = item
    }

    for (i in dst) {
        let item2 = dst[i]
        let id = item2.id;
        let item1 = table[id]
        if (item1 != null && item2 != null) {
            item2.state = item1.state;
            item2.error = item1.error;
        }
    }
}


////////////////////////////////////////////////////////////////////////////////

const state = {
    items: []
};

const getters = {
    items(state) {
        return state.items
    }
}

const mutations = {

    handle_fetch_vo(state, vo) {
        state.items = vo.setup_items
    },

    handle_apply_vo(state, vo) {
        let srclist = vo.setup_items;
        let dstlist = state.items;
        let table = {}
        updateStateForItems(srclist, table, dstlist)
    },

};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = 'setup'
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_fetch_vo', vo)
                return Promise.resolve(r)
            })
    },


    apply(context, p) {
        let method = 'POST'
        let type = 'setup'
        let data = p.data;
        return context.dispatch("rest/execute", { method, type, data }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_apply_vo', vo)
                return Promise.resolve(r)
            })
    },

    skipall(context) {
        let method = 'POST'
        let type = 'setup/skip-all'
        let data = {};
        return context.dispatch("rest/execute", { method, type, data }, use_root)
            .then((r) => {
                // let vo = r.data;
                // context.commit('handle_apply_vo', vo)
                return Promise.resolve(r)
            })
    },
};


export default {
    name: "module_setup",
    namespaced: true,
    state, mutations, actions, getters
}
