
import PathJS from './path.js'


/**
  pathbasedefaultValue
*/
function Get(base, path, defaultValue) {

    if (base == null || path == null) {
        return defaultValue
    }

    let p = base;
    let elist = PathJS.ToElements(path)
    elist = PathJS.NormalizeElements(elist)

    for (var i in elist) {
        let key = elist[i]
        let next = p[key]
        if (next == null) {
            return defaultValue
        } else {
            p = next;
        }
    }

    if (p == null) {
        return defaultValue
    }

    return p
}


export default {
    name: "GetterJS",
    Get
}
