
import StateJS from '@/js/state.js'
import GetterJS from '@/js/getter.js'

const use_root = { root: true }

////////////////////////////////////////////////////////////////////////////////

function Filter() {

}

Filter.prototype = {}

////////////////////////////////////////////////////////////////////////////////

function listItemsFromVO(vo, fn_accept) {
    let dst = []
    let src = GetterJS.Get(vo, "content_types", [])
    for (var i in src) {
        let item = src[i]
        if (fn_accept(item)) {
            dst.push(item)
        }
    }
    return dst;
}

// ID'.'
function stringifyIDsFromItems(items) {
    let dst = ""
    for (var i in items) {
        let id = items[i].id;
        dst += ("." + id)
    }
    return dst;
}

////////////////////////////////////////////////////////////////////////////////

const state = {

    file_type_list: [],

    project_type_list: [],

};

const filterObj = new Filter();
const state_rw = StateJS.createStateRW({ pk: "id", listname: "content_types" })
state_rw.init(state)
state_rw.setFilter(state, { text: "", filter: filterObj })
state_rw.setPage(state, { limit: 10, offset: 0 })



const getters = {
    list(state) { return state.items },

    file_type_list(state) { return state.file_type_list },

    project_type_list(state) { return state.project_type_list },

    all(state) { return state_rw.listAll(state) },
}

const mutations = {

    handle_vo(state, vo) {
        state_rw.reset(state)
        state_rw.updateVO(state, vo)

        state.file_type_list = listItemsFromVO(vo, (item) => { return item.as_project == false })
        state.project_type_list = listItemsFromVO(vo, (item) => { return item.as_project == true })
    },

    remove_item(state, item) {
        state_rw.remove(state, item.id)
    },
};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = 'content-types'
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_vo', vo)
            })
    },

    insert(context, { item }) {
        let method = 'POST'
        let type = 'content-types'
        let data = {
            content_types: [item]
        }
        return context.dispatch("rest/execute", { method, type, data }, use_root)
            .then(() => {
                // let vo = r.data;
                context.dispatch('fetch')
            })
    },

    update(context, { item }) {
        let method = 'PUT'
        let type = 'content-types'
        let id = item.id;
        let data = {
            content_types: [item]
        }
        return context.dispatch("rest/execute", { method, type, id, data }, use_root)
            .then(() => {
                // let vo = r.data;
                context.dispatch('fetch')
            })
    },

    remove(context, p1) {

        let id = p1.id
        let item = p1.item
        let items = p1.items
        let params = {}

        if (id != null) {
            // NOP
        } else if (item != null) {
            id = item.id
        } else if (items != null) {
            params.ids = stringifyIDsFromItems(items)
        } else {
            return Promise.reject("no param:id|item|items")
        }

        let method = 'DELETE'
        let type = 'content-types'
        let p2 = { method, type, id, params }
        return context.dispatch("rest/execute", p2, use_root)
            .then((r) => {
                if (id != null) {
                    context.commit('remove_item', { id })
                } else if (items != null) {
                    context.dispatch('fetch')
                }
                return Promise.resolve(r)
            })
    },

};


export default {
    name: "module_project_type",
    namespaced: true,
    state, mutations, actions, getters
}
