

import StateJS from '@/js/state.js'


const use_root = { root: true }


////////////////////////////////////////////////////////////////////////////////

function sortItems(list) {
    if (list == null) {
        return
    }
    list.sort((o1, o2) => {
        let s1 = "" + o1.name
        let s2 = "" + o2.name
        return s1.localeCompare(s2)
    })
}

////////////////////////////////////////////////////////////////////////////////


function Filter() {
    this.text = "";
}


Filter.prototype = {

    containString(keyword, text) {
        if ((keyword == null) || (text == null)) { return false }

        text = this.normalizeText(text)
        keyword = this.normalizeText(keyword)

        let i = text.indexOf(keyword)
        return (i >= 0)
    },

    normalizeText(text) {
        if (text == null || text == "") { return "" }
        return text.trim().toLowerCase()
    },

    prepare(text) {
        this.text = text;
    },

    accept(item) {
        let filter = this.text;
        if (this.containString(filter, item.name)) { return true }
        if (this.containString(filter, item.title)) { return true }
        if (this.containString(filter, item.description)) { return true }
        // if (this.containString(filter, item.path)) { return true }
        return false;
    },
}


////////////////////////////////////////////////////////////////////////////////


function FetchRequest(context, p) {
    if (p == null) {
        p = {}
    }
    this.context = context;
    this.ids = p.ids;
}

FetchRequest.prototype = {

    prepareFetchByIds(ids) {
        let p = {}
        p.ids = this.makeIDs(ids)
        return p
    },

    makeIDs(ids) {
        if (ids == null) { return null; }
        let count = 0;
        let str = ""
        for (var i in ids) {
            let id = ids[i];
            str += (id + ".")
            count++;
        }
        if (count < 1) { return null; }
        return str
    },
}


function getCurrentPageItemIds(context) {
    let state = context.state;
    let items = state_rw.listCurrentPage(state)
    let dst = []
    for (var i in items) {
        let id = items[i].id;
        dst.push(id)
    }
    return dst;
}


////////////////////////////////////////////////////////////////////////////////


const state = {
};

const filterObj = new Filter();
const state_rw = StateJS.createStateRW({ pk: "id", listname: "local_repositories" })
state_rw.init(state)
state_rw.setFilter(state, { text: "", filter: filterObj })
state_rw.setPage(state, { limit: 10, offset: 0 })


const getters = {

    all(state) {
        return state_rw.listAll(state)
    },

    currentPageItems(state) {
        return state_rw.listCurrentPage(state)
    },

    currentPage(state) {
        return state_rw.currentPageNumber(state)
    },

    pageSize(state) {
        return state_rw.limit(state)
    },

    total(state) {
        return state_rw.total(state)
    },

}

const mutations = {
    update(state, p) {

        let limit = p.size // page-size
        let index = p.index // page-index
        let filter = p.filter
        let vo = p.vo;

        if (limit != null || index != null) {
            state_rw.setPage(state, { limit, index })
        }

        if (filter != null) {
            state_rw.setFilter(state, { text: filter })
        }

        if (vo != null) {
            sortItems(vo.local_repositories)
            state_rw.updateVO(state, vo)
        }
    },

    reset(state) {
        state_rw.reset(state)
    }
};

const actions = {
    fetch(context) {
        return context.dispatch('fetchAll')
    },

    fetchAll(context) {
        let method = 'GET'
        let type = 'local-repositories'
        let params = { all: 1 }
        let p = { method, type, params }
        return context.dispatch("rest/execute", p, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('reset')
                context.commit('update', { vo })
                context.dispatch('fetchByIds', {})
                return Promise.resolve(r)
            })
    },

    fetchByIds(context, { ids }) {

        if (ids == null) {
            ids = getCurrentPageItemIds(context)
        }

        let req = new FetchRequest(context)
        let params = req.prepareFetchByIds(ids)
        params['with-file-state'] = 1
        params['with-projects'] = 1

        let method = 'GET'
        let type = 'local-repositories'
        let p = { method, type, params }
        return context.dispatch("rest/execute", p, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('update', { vo })
                return Promise.resolve(r)
            })
    },


    fetchById(context, { id }) {

        if (id == null) {
            return Promise.reject("no param: id")
        }

        let params = {}
        params['with-file-state'] = 1
        params['with-submodules'] = 1
        params['with-worktrees'] = 1
        params['with-projects'] = 1

        let method = 'GET'
        let type = 'local-repositories'
        let p = { method, type, id, params }

        return context.dispatch("rest/execute", p, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('update', { vo })
                return Promise.resolve(r)
            })
    },


    update(context, item) {
        let method = 'PUT'
        let type = 'local-repositories'
        let id = item.id;
        let data = {
            local_repositories: [item]
        }
        let p = { method, type, id, data }
        return context.dispatch("rest/execute", p, use_root)
            .then((r) => {
                context.dispatch("fetch")
                return Promise.resolve(r)
            })
    },

    remove(context, item) {
        let method = 'DELETE'
        let type = 'local-repositories'
        let id = item.id;
        let p = { method, type, id }
        return context.dispatch("rest/execute", p, use_root)
            .then((r) => {
                context.dispatch("fetch")
                return Promise.resolve(r)
            })
    },

};


export default {
    name: "module_local_repository",
    namespaced: true,
    state, mutations, actions, getters
}
