


const level_trace = 'trace'
const level_debug = 'debug'
const level_info = 'info'
const level_warn = 'warn'
const level_error = 'error'
const level_fatal = 'fatal'

////////////////////////////////////////////////////////////////////////////////


function RW(state) {
    this.state = state;
}

RW.prototype = {

    makeDefaultItem() {
        let now = this.now()
        let id = this.nextID();
        return {
            id: id,
            level: level_debug,
            type: "default", // [error|warn|success|default]
            action: "", // [fetch|insert|update|remove]
            message: "",
            time: now,
            read: false,
            display: true,
            source: "",
            duration: 0,
        }
    },

    normalizeItem(item) {
        if (item == null) {
            item = {}
        }
        item.id = null;
        item.time = null;
        item.read = false;
        let di = this.makeDefaultItem()
        for (var key in di) {
            if (item[key] == null) {
                item[key] = di[key]
            }
        }
        return item;
    },

    nextID() {
        let id = this.state.revision + 1;
        this.state.revision = id;
        return id;
    },

    now() {
        let d = new Date();
        return d.getTime();
    },

    clean() {
        let state = this.state;
        state.logs = this.cleanList(state.logs, (item) => {
            return !item.read;
        })
        state.todo = this.cleanList(state.todo, (item) => {
            return !item.read;
        })
    },

    cleanList(srclist, keep_fn) {
        let dstlist = []
        if (srclist == null) { return dstlist }
        for (var i in srclist) {
            let item = srclist[i]
            if (keep_fn(item)) {
                dstlist.push(item)
            }
        }
        return dstlist
    },

}

////////////////////////////////////////////////////////////////////////////////

const state = {
    logs: [],
    todo: [],
    revision: 0,
};

const mutations = {

    push(state, item) {
        let rw = new RW(state);
        item = rw.normalizeItem(item)
        state.logs.push(item);
        state.todo.push(item);
    },

    clean(state) {
        let rw = new RW(state);
        rw.clean()
    },

};

const actions = {
};

const getters = {
    logs(state) {
        return state.logs;
    },

    todo(state) {
        return state.todo;
    },

    revision(state) {
        return state.revision;
    },
}


export default {
    name: "module_log",
    namespaced: true,
    state, mutations, actions, getters
}
