import IntentJS from '@/js/intent.js'

const use_root = { root: true }


let the_revision_count = 0


////////////////////////////////////////////////////////////////////////////////


function isIntentReady(intent) {

    if (intent == null) {
        return false
    }

    return false
}

////////////////////////////////////////////////////////////////////////////////

const state = {
    intents: [],
    revision: 0,
    path: "",
    name: "",
    type: "",
    typetext: "",
    current: {}, // a intent item
};

const getters = {
    list(state) {
        return state.intents
    },

    revision(state) {
        return state.revision
    },

    current(state) {
        return state.current
    },

    type(state) {
        return state.type
    },

    typetext(state) {
        return state.typetext
    },

    path(state) {
        return state.path
    },

    name(state) {
        return state.name
    },
}

const mutations = {

    handle_intents(state, vo) {
        if (vo == null) { return }
        let intents = vo.intents
        if (intents == null) { return }
        state.intents = intents
    },


    setCurrent(state, item) {
        if (item == null) { return }

        let r = ++the_revision_count;
        let w = IntentJS.Wrap(item)

        item.revision = r;

        state.type = w.GetType()
        state.typetext = w.GetTypeText()
        state.path = w.GetPath()
        state.name = w.GetName()
        state.current = item;
        state.revision = r;
    },

};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = 'intents'
        let task = context.dispatch("rest/execute", { method, type }, use_root)
        task.then((r) => {
            let vo = r.data;
            context.commit('handle_intents', vo)
        })
        return task;
    },

    run(context, vo) {
        let method = 'POST'
        let type = 'intents'
        let data = vo;
        let task = context.dispatch("rest/execute", { method, type, data }, use_root)
        task.then((r) => {
            let vo = r.data;
            context.commit('handle_intents', vo)
        })
        return task;
    },

    start(context, vo) {
        let item = vo.intents[0];
        if (isIntentReady(item)) {
            return context.dispatch('run', vo)
        }
        context.commit('setCurrent', item)
        // return Promise.reject("intent is not ready");
        return Promise.resolve(vo)
    },

};



export default {
    name: "module_intent",
    namespaced: true,
    state, mutations, actions, getters
}
