
import StateJS from './../js/state.js'

const use_root = { root: true }


////////////////////////////////////////////////////////////////////////////////

function is_get_all(p) {
    if (p == null) { return false; }
    let all = p.all;
    if (all == null) { return false; }
    return (all == true || all == 1)
}

// ID'.'
function stringifyIDsFromItems(items) {
    let dst = ""
    for (var i in items) {
        let id = items[i].id;
        dst += ("." + id)
    }
    return dst;
}

////////////////////////////////////////////////////////////////////////////////

function Filter() { }

Filter.prototype = {}

////////////////////////////////////////////////////////////////////////////////

const state = {
};

const filterObj = new Filter();
const state_rw = StateJS.createStateRW({ pk: "id", listname: "mediae" })
state_rw.init(state)
state_rw.setFilter(state, { text: "", filter: filterObj })
state_rw.setPage(state, { limit: 10, offset: 0 })


const getters = {
    list(state) {
        return state_rw.listCurrentPage(state)
    },

    total(state) {
        return state_rw.total(state)
    }
}

const mutations = {

    handle_vo(state, vo) {
        state_rw.updateVO(state, vo)
    },

    reset(state, p) {
        state_rw.reset(state, p)
    },

    remove_id(state, id) {
        state_rw.remove(state, id)
    },

    update(state, p) {
        if (p == null) { return }
        let vo = p.vo;
        let limit = p.size;
        let index = p.index;
        let text = p.filter;

        if (limit != null || index != null) {
            state_rw.setPage(state, { limit, index })
        }

        if (text != null) {
            state_rw.setFilter(state, { text })
        }

        if (vo != null) {
            state_rw.updateVO(state, vo)
        }
    },
};

const actions = {
    fetch(context, params) {
        let all = is_get_all(params);
        let method = 'GET'
        let type = 'media'
        return context.dispatch("rest/execute", { method, type, params }, use_root)
            .then((r) => {
                let vo = r.data;
                if (all) {
                    context.commit('reset', { list: true })
                }
                context.commit('handle_vo', vo)
            })
    },

    insert(context, p) {
        let item = p.item;
        let method = 'POST'
        let type = 'media'
        let data = {
            mediae: [item]
        }
        if (item == null) {
            return Promise.reject("no media item")
        }
        return context.dispatch("rest/execute", { method, type, data }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_vo', vo)
            })
    },

    import_presets(context) {
        let method = 'POST'
        let type = 'media'
        let id = 'import-presets'
        let data = {}
        return context.dispatch("rest/execute", { method, type, id, data }, use_root)
            .then((r) => {
                // let vo = r.data;
                // context.commit('handle_vo', vo)
                return Promise.resolve(r)
            })
    },

    remove(context, p1) {

        let id = p1.id
        let item = p1.item
        let items = p1.items
        let params = {}

        if (id != null) {
            // NOP
        } else if (item != null) {
            id = item.id
        } else if (items != null) {
            params.ids = stringifyIDsFromItems(items)
        } else {
            return Promise.reject("no param:id|item|items")
        }

        let method = 'DELETE'
        let type = 'media'
        let p2 = { method, type, id, params }
        return context.dispatch("rest/execute", p2, use_root)
            .then((r) => {
                if (id != null) {
                    context.commit('remove_id', id)
                } else if (items != null) {
                    context.dispatch('fetch', { all: true })
                }
                return Promise.resolve(r)
            })
    },

};


export default {
    name: "module_media",
    namespaced: true,
    state, mutations, actions, getters
}
