
import { ElDialog } from 'element-plus';

<template>
    <ElDialog title="">
        <div class="dialog-2333">

            <h2> @{{ repo.name }} </h2>
            <div> {{ repo.description }}</div>

            <el-divider />

            <ElForm label-width="180">

                <ElFormItem label="">
                    <!-- <div> {{ repo.path }} </div> -->
                    <ElInput v-model="repo.path" readonly></ElInput>
                </ElFormItem>

                <ElFormItem label="">
                    <ElRadioGroup class="project-list" v-model="currentProject">
                        <ElRadio v-for="project in repoProjects" :label="project.path" :title="project.name"> {{
                            project.path }} </ElRadio>
                    </ElRadioGroup>
                </ElFormItem>

                <ElFormItem label="">
                    <ElInput v-model="fullpath" readonly></ElInput>
                </ElFormItem>

                <ElFormItem label="">
                    <div class="with-app-list">
                        <div class="with-app" v-for="item in theWithAppList" :key="item.id" :title="item.title"
                            @click="handleClickApp(item)"> <img class="with-app-icon" :src="item.icon" />

                            <el-icon>
                                <VideoPlay />
                            </el-icon>
                        </div>
                    </div>
                </ElFormItem>
            </ElForm>

            <div>
                <ElButton @click="close"></ElButton>
            </div>
        </div>
    </ElDialog>
</template>

<script setup>
import { List, VideoPlay } from "@element-plus/icons-vue";
</script>

<script>
import { ElButton, ElRadioGroup, ElRadio, ElForm, ElFormItem, ElInput } from 'element-plus';
import MyApps from './open-with-list.vue'
import PathJS from './../../js/path.js'

export default {
    name: "home-local-repository-open-item-dialog",
    components: { ElButton, MyApps, ElRadioGroup, ElForm, ElFormItem, ElInput },

    computed: {
        repo() {
            let d = this.data;
            if (d == null) {
                d = {}
            }
            return d;
        },

        repoTitle() {
            return this.repo.name;
        },

        repoLocation() {
            return this.repo.path;
        },

        repoProjects() {
            let list = this.repo.projects;
            if (list == null) {
                list = []
            }
            return list;
        },

        theWithAppList() {
            return this.$store.getters["executable/openWithList"];
        }
    },

    data() {
        return {
            fullpath: "",
            currentProject: "."
        }
    },

    methods: {
        close() {
            this.$emit('update:modelValue', false)
        },

        computeFullPath(path) {
            if (path == null) {
                path = this.currentProject
            }
            let wkdir = this.data.workspace_path;
            let to = wkdir + '/' + path;
            return PathJS.NormalizePath(to)
        },

        handleClickApp(app) {
            let dir = this.fullpath;
            if (app == null || dir == null) { return; }
            let intent = {
                executable: app,
                wd: dir
            };
            let vo = {
                intents: [intent],
            };
            this.$store.dispatch("intent/run", vo);
        }
    },

    props: {
        data: Object,
    },

    watch: {
        currentProject(value) {
            this.fullpath = this.computeFullPath(value)
        },

        repoLocation() {
            this.currentProject = '.';
        }
    }
}
</script>

<style scoped >
.dialog-2333 {
    text-align: left;
}

.project-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.with-app-list {
    display: flex;
    flex-direction: row;
}

.with-app {
    border-color: rgba(128, 128, 128, 0.116);
    border-width: 1px;
    border-style: solid;
    border-radius: 2px;
    cursor: pointer;
    margin: 5px;
    padding: 3px;
    color: rgba(128, 128, 128, 0.103);
}

.with-app:hover {
    border-color: gray;
    color: gray;
}

.with-app-icon {
    width: 32px;
    height: 32px;
}
</style>
