

// 
function NormalizeElements(elements) {
    let src = elements;
    let dst = []
    if (src == null) {
        return dst;
    }
    for (var i in src) {
        let el = src[i]
        if (el == null) {
            // skip
        } else if (el == '') {
            // skip
        } else if (el == '.') {
            // skip
        } else if (el == '..') {
            dst.pop()
        } else {
            dst.push(el)
        }
    }
    return dst
}


// 
function NormalizePath(path) {
    let elist = ToElements(path)
    elist = NormalizeElements(elist)
    return Stringify(elist)
}


// 
function ToElements(path) {
    const sep1 = '\\'
    const sep2 = "/"
    let path1 = path + ""
    let p2 = path1.replaceAll(sep1, sep2)
    let list = p2.split(sep2)
    return list
}


// 
function Stringify(elements, sep1, sep2) {
    if (sep1 == null) {
        sep1 = "/"
    }
    if (sep2 == null) {
        sep2 = "/"
    }
    let sep = sep1;
    let str = ""
    if (elements == null) {
        return str;
    }
    for (var i in elements) {
        let el = elements[i]
        str += sep + el;
        sep = sep2;
    }
    return str;
}


export default {
    name: "path-js",
    NormalizeElements,
    NormalizePath,
    Stringify,
    ToElements,
}
