<template>
  <div><MyDrawer v-model="displayDrawer" /></div>
</template>
    
<script>
import MyDrawer from "./open-with-drawer.vue";

export default {
  name: "open-with-listener",

  components: { MyDrawer },

  computed: {
    revision() {
      return this.$store.getters["intent/revision"];
    },
    current() {
      return this.$store.getters["intent/current"];
    },
  },

  data() {
    return {
      displayDrawer: false,
    };
  },

  watch: {
    revision() {
      this.displayDrawer = true;
    },
  },
};
</script>
