<template>
  <div>
    <h3>Git : {{ item.name }}</h3>

    <el-form label-width="50px">
      <el-form-item label=""> {{ item.path }} </el-form-item>
      <el-form-item label="">
        <MyStateBox
          v-model="item.state"
          :targetId="item.id"
          @click-import="handleImport"
        />
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import MyStateBox from "./state-box-with-import-button.vue";

export default {
  name: "import-fast-info-of-repository",

  components: { MyStateBox },

  computed: {
    item() {
      let it = this.modelValue;
      if (it == null) {
        it = {};
      }
      return it;
    },
  },

  methods: {
    handleImport() {
      let name = "save";
      let items = ["repository"];
      this.$emit("action", { name, items });
    },
  },

  props: {
    modelValue: Object,
  },
};
</script>

<style scoped>
.btn-import {
  text-align: right;
}
</style>
