<template>
  <div class="item">
    <MyOverview :data="data" @action="handleAction" :expanded="expanded" />

    <div class="gap" v-show="expanded" />

    <MyDetail :data="data" @action="handleAction" v-show="expanded" />
  </div>
</template>

<script>
import MyDetail from "./item-detail.vue";
import MyOverview from "./item-overview.vue";
// import MyOpenWithList from "./open-with-list.vue";

export default {
  name: "home-local-repository-item",

  components: { MyDetail, MyOverview },

  computed: {
    apps() {
      return this.$store.getters["executable/openWithList"];
    },
  },

  methods: {
    handlePopoverShow() {
      let id = this.data.id;
      console.warn("todo: load-local-repo-detail:" + id);
    },

    handleClickItemName() {
      // this.openWithDefaultExe();
      let id = this.data.id;
      this.$router.push("/repositories/" + id);
    },

    handleClickItemPath() {
      this.openWithDefaultExe();
    },

    handleAction(e) {
      let name = e.name;
      if (name == "open-with") {
        this.openWith(e);
      } else if (name == "open-item-more") {
        this.openItemMore(e);
      }
      this.$emit("action", e);
    },

    openItemMore(e) {
      e.item = this.data;
      this.$emit("action", e);
    },

    openWithDefaultExe() {
      let apps = this.apps;
      let e = {};
      for (var i in apps) {
        let app = apps[i];
        if (app != null) {
          e.executable = app;
          this.openWith(e);
          break;
        }
      }
    },

    openWith(e) {
      e.repository = this.data;
      // console.log("do-open-with, exe_id:" + eid + " repo_id:" + rid);
      let intent = {
        executable: e.executable,
        repository: e.repository,
      };
      let vo = {
        intents: [intent],
      };
      this.$store.dispatch("intent/run", vo);
    },
  },

  props: {
    data: Object,
    expanded: Boolean,
  },
};
</script>

