<template>
  <el-dialog title=" |  | " align-center width="800px">
    <div class="this-dialog">
      <div class="my-path-bar">
        <el-input v-model="path" placeholder="">
          <template #prepend>  </template>
          <template #append>
            <el-button @click="browse"> ... </el-button>
          </template>
        </el-input>

        <el-button style="margin-left: 10px" @click="search">
          <el-icon>
            <Search />
          </el-icon> 
        </el-button>
      </div>

      <div>
        <MyInfoRepository class="item-info-box" v-model="repository" v-show="has_repository" @action="handleAction" />

        <MyInfoWorktree class="item-info-box" v-model="worktree" x-hide-v-show="has_worktree" v-show="false"
          @action="handleAction" />

        <MyInfoProject class="item-info-box" v-model="project" v-show="has_project" @action="handleAction" />

        <div class="item-info-box no-result-text" v-show="displayNoResult">
           :(
        </div>
      </div>

      <div>
        <!-- <el-button type="success">  </el-button>
        <el-button type="default">  </el-button> -->
      </div>

      <OpenFileDialog v-model="displayOpenDialog" @open-file="handleOpenPathChange" />
    </div>
  </el-dialog>
</template>

<script setup>
import { Search } from "@element-plus/icons-vue";
</script>

<script>
import MyInfoProject from "./info-of-project.vue";
import MyInfoRepository from "./info-of-repository.vue";
import MyInfoWorktree from "./info-of-worktree.vue";
import OpenFileDialog from "@/components/filebrowser/dialog-open.vue";

export default {
  name: "import-fast-dialog",

  components: {
    MyInfoProject,
    MyInfoRepository,
    MyInfoWorktree,
    OpenFileDialog,
  },

  computed: {
    displayNoResult() {
      let b0 = this.has_search_done;
      let b1 = this.has_project;
      let b2 = this.has_repository;
      let b3 = this.has_worktree;
      return b0 && !b1 && !b2 && !b3;
    },
  },

  data() {
    return {
      path: "",
      project: {},
      repository: {},
      worktree: {},
      has_project: false,
      has_repository: false,
      has_worktree: false,
      has_search_done: false,
      result_vo: {},
      displayOpenDialog: false,
    };
  },

  methods: {
    search() {
      let path = this.path;
      let method = "POST";
      let type = "repository-worktree-project/find";
      let data = { path };
      this.$store.dispatch("rest/execute", { method, type, data }).then((r) => {
        let vo = r.data;
        this.handleSearchResults(vo);
      });
    },

    browse() {
      this.displayOpenDialog = true;
    },

    save(e) {
      let path = this.result_vo.path;
      let method = "POST";
      let type = "repository-worktree-project/save";
      let data = { path };

      let keys = e.items;
      let src = this.result_vo;
      let dst = data;
      for (var i in keys) {
        let key = keys[i];
        dst[key] = src[key];
      }

      this.$store.dispatch("rest/execute", { method, type, data }).then(() => {
        this.search();
      });
    },

    makeObjectWithoutNil(o) {
      if (o == null) {
        o = {};
      }
      return o;
    },

    handleAction(e) {
      let name = e.name;
      if (name == "save") {
        this.save(e);
      }
    },

    handleSearchResults(vo) {
      this.project = this.makeObjectWithoutNil(vo.project);
      this.repository = this.makeObjectWithoutNil(vo.repository);
      this.worktree = this.makeObjectWithoutNil(vo.worktree);

      this.has_project = vo.project != null;
      this.has_repository = vo.repository != null;
      this.has_worktree = vo.worktree != null;

      this.has_search_done = true;
      this.result_vo = vo;
    },

    handleOpenPathChange(files) {
      let p = "";
      for (var i in files) {
        let f = files[i];
        p = f.path;
      }
      this.path = p;
    },
  },
};
</script>

<style scoped>
.item-info-box {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  border-color: rgb(199, 199, 199);
  border-style: solid;
  border-width: 1px;
  border-radius: 5px;
}

.this-dialog {
  text-align: left;
}

.no-result-text {
  text-align: center;
  color: gray;
  padding-top: 30px;
  padding-bottom: 30px;
}

.my-path-bar {
  display: flex;
  flex-direction: row;
}
</style>
