<template>
  <div><MyNewVersionDialog v-model="displayNewVersionDialog" /></div>
</template>

<script>
import MyNewVersionDialog from "@/components/checkupdate/new-version-dialog.vue";

export default {
  name: "init-element",

  components: { MyNewVersionDialog },

  data() {
    return {
      displayNewVersionDialog: false,
    };
  },

  methods: {
    init() {
      this.fetch();
    },

    fetch() {
      this.$store.dispatch("init/fetch").then((r) => {
        let vo = r.data;
        this.handleVO(vo);
      });
    },

    handleVO(vo) {
      if (this.checkSetup(vo)) {
        return;
      }
      if (this.checkNewVersion(vo)) {
        return;
      }
    },

    checkSetup(vo) {
      let vo2 = vo.Setup;
      let req = vo2.setup_required;
      if (req) {
        this.doSetup();
        return true;
      }
      return false;
    },

    checkNewVersion(vo) {
      let vo2 = vo.CheckUpdate;
      this.$store.commit("checkupdate/handle_vo", vo2);
      let hasNew = vo2.checkupdate.has_new_version;
      let hasIgnored = vo2.checkupdate.has_ignored;
      if (hasNew && !hasIgnored) {
        this.doShowNewVersion();
        return true;
      }
      return false;
    },

    doSetup() {
      this.$router.push("/init/setup-or-import");
    },

    doShowNewVersion() {
      this.displayNewVersionDialog = true;
    },
  },

  mounted() {
    this.init();
  },
};
</script>
