<template>
  <div class="exe-list">
    <div
      class="exe-list-item"
      v-for="item in items"
      :key="item.id"
      :title="item.title"
      @click="handleClickItem(item)"
    >
      <div class="exe-list-item-inner">
        <img class="exe-list-item-icon" :src="item.icon" />
      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "open-with-panel",

  computed: {
    items() {
      return this.$store.getters["executable/openWithList"];
    },
  },

  data() {
    return {
      targetType: "",
      targetPath: "/a/b/c/d",
    };
  },

  methods: {
    handleClickItem(item) {
      let name = "intent";
      let intent = { executable: item };
      let e = { name, intent };
      this.$emit("action", e);
    },
  },
};
</script>
  
