<template>
  <div class="open-with-panel">
    <el-input :modelValue="path" readonly>
      <template #prepend>  </template>
      <template #append>
        <el-button @click="copy" title="">
          <el-icon>
            <CopyDocument />
          </el-icon>
          <!--  -->
        </el-button>
      </template>
    </el-input>

    <!-- <h3></h3> -->

    <div style="margin-top: 20px;">: </div>

    <MyExeList @action="handleAction" />
  </div>
</template>

<script setup>
import { CopyDocument } from "@element-plus/icons-vue";
</script>

<script>
import MyExeList from "./open-with-exe-list.vue";
import { ElMessage } from "element-plus";
import Intent from '@/js/intent.js'


export default {
  name: "open-with-panel",

  components: { MyExeList },

  computed: {
    current() {
      let cur = this.$store.getters["intent/current"];
      if (cur == null) {
        cur = {};
      }
      return cur;
    },

    revision() {
      return this.$store.getters["intent/revision"];
    },

    path() {
      return this.$store.getters["intent/path"];
    },

    type() {
      return this.$store.getters["intent/type"];
    },

    name() {
      return this.$store.getters["intent/name"];
    },
  },

  data() {
    return {

    };
  },

  methods: {
    handleAction(e) {
      let name = e.name;
      if (name == "intent") {
        return this.handleIntent(e.intent);
      }
      this.$emit('action', e)
    },

    handleIntent(intent) {
      if (intent == null) {
        return;
      }
      let src1 = intent;
      let src2 = this.current;
      let dst = {};
      this.copyfields(src1, dst);
      this.copyfields(src2, dst);
      let vo = { intents: [dst] };
      this.$store.dispatch("intent/run", vo);
    },

    copyfields(src, dst) {
      for (var k in src) {
        dst[k] = src[k];
      }
    },

    copy() {
      let text = this.path;
      this.$store.dispatch("clipboard/copy", text);
    },

    // reload() {
    //   let src = this.current;
    //   let holder = Intent.Load(src);
    //   this.xx = holder.xx
    //   this.yy = holder.yy
    //   this.zz = holder.zz
    // },
  },

  mounted() {

  },

  watch: {
    revision() {

    },
  },
};
</script>

<style>
.open-with-panel {
  text-align: left;
}
</style>
