
import axios from 'axios'

const use_root = { root: true }

function nop() { }



axios.interceptors.request.use(function (config) {
    // console.log("axios.interceptors.request.use")
    return config;
}, function (error) {
    return Promise.reject(error);
});

axios.interceptors.response.use(function (response) {
    // console.log("axios.interceptors.response.use")
    return response;
}, function (error) {
    return Promise.reject(error);
});

////////////////////////////////////////////////////////////////////////////////


function Request(ctx) {
    this.context = ctx;
    this.workCount = 0;
    this.method = '';
    this.action = 'fetch';
}

Request.prototype = {

    updateWorkCount(delta) {
        let old = this.workCount;
        let next = old + delta;
        if (old == 0 && next == 1) {
            this.context.commit('updateWorking', delta)
        } else if (old == 1 && next == 0) {
            this.context.commit('updateWorking', delta)
        }
        this.workCount = next;
    },

    forAxios() {
        let p = {}
        this.forAxiosField(p, "method")
        this.forAxiosField(p, "params")
        this.forAxiosField(p, "data")
        this.forAxiosField(p, "url")

        this.method = p.method;
        this.updateWorkCount(1)
        return p;
    },

    forAxiosField(p, name) {
        let value = this[name];
        if (value == null) {
            return;
        }
        p[name] = value;
    },

    resolve(/* r */) {
        // handle ok
        this.updateWorkCount(-1)

        let is_get = this.isGetMethod()

        // push log
        // let vo = r.data;
        let log = {}
        log.message = ''; // vo.message;
        log.display = !is_get;
        log.action = this.action;
        log.level = 'info';
        log.type = 'success';
        log.duration = 3000;
        log.source = 'axios';
        this.context.commit('log/push', log, use_root)
    },

    reject(r) {
        // handle error
        nop(r)
        this.updateWorkCount(-1)

        let vo = r.response.data;
        let msg = vo.error;
        if (msg == null) {
            msg = r.message;
        }

        // push log
        let log = {}
        log.message = msg;
        log.display = true;
        log.action = this.action;
        log.level = 'error';
        log.type = 'error';
        log.duration = 0;
        log.source = 'axios';
        this.context.commit('log/push', log, use_root)
    },

    isGetMethod() {
        let action = this.action;
        let method = this.method;
        if (action == null) {
            action = ""
        }
        if (method == null || method == '') {
            method = 'GET'
        }
        method = method.trim().toUpperCase()
        action = action.trim().toUpperCase()
        return (method == 'GET') || (action == "FETCH")
    },
}


////////////////////////////////////////////////////////////////////////////////

function RequestBuilder(ctx) {
    this.context = ctx;
    this.action = '';
}


RequestBuilder.prototype = {

    init(p) {
        this.action = p.action;
        this.method = p.method;
        this.url = p.url;
        this.params = p.params;
        this.data = p.data;
        this.type = p.type;
        this.id = p.id;
    },

    create() {
        let req = new Request(this.context);
        req.action = this.makeAction();
        req.method = this.makeMethod();
        req.url = this.makeURL();
        if (this.params != null) {
            req.params = this.params;
        }
        if (this.data != null) {
            req.data = this.data;
        }
        return req;
    },

    makeAction() {
        let action = this.action;
        if (action == null || action == '') {
            let method = this.makeMethod();
            const map = {
                "GET": "fetch",
                "POST": "insert",
                "PUT": "update",
                "DELETE": "remove",
            }
            action = map[method]
        }
        if (action == null || action == '') {
            action = 'fetch'
        }
        return action.toLowerCase();
    },

    makeMethod() {
        let method = this.method;
        if (method == null) {
            method = "GET"
        }
        return method.toUpperCase();
    },

    makeURL() {
        let url = this.url;
        let type = this.type;
        let id = this.id;
        if (url == null) {
            url = this.getBaseRestURL() + type
            if (id != null) {
                url += ("/" + id)
            }
        }
        return url
    },

    getBaseRestURL() {
        return "/api/v1/"
    },

}




////////////////////////////////////////////////////////////////////////////////

const state = {
    working: false,
    workCount: 0,
};

const getters = {
    working(state) {
        return state.working;
    }
}

const mutations = {
    updateWorking(state, n) {
        let count = state.workCount;
        if (n < 0) {
            count--;
        } else if (n > 0) {
            count++;
        }
        if (count > 0) {
            state.working = true;
        } else {
            state.working = false;
        }
        state.workCount = count;
    }
};

const actions = {

    execute(context, p) {
        let rb = new RequestBuilder(context);
        rb.init(p);
        let task = rb.create()
        return axios(task.forAxios())
            .then((r) => {
                task.resolve(r);
                return Promise.resolve(r)
            }).catch((r) => {
                task.reject(r)
                return Promise.reject(r)
            })
    }
};


export default {
    name: "module_rest",
    namespaced: true,
    state, mutations, actions, getters
}
