<template>
  <div class="open-with-list">
    <div class="open-with-item" v-for="item in apps" :key="item.id" :title="item.title" @click="handleClickItem(item)">
      <img class="icon" :src="item.icon" />
    </div>

    <!-- <div class="open-with-item" title="" @click="handleClickItemMore">     
      <el-icon>
        <More />
      </el-icon>
    </div> -->
  </div>
</template>

<script setup>
import { More } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "home-local-repository-open-with-list",

  computed: {
    apps() {
      return this.$store.getters["executable/openWithList"];
    },
  },

  data() {
    return {
      //   apps: [],
    };
  },

  methods: {
    handleClickItem(item) {
      let executable = item;
      let repository = this.getSimpleRepository();
      let intent = { executable, repository };
      // let name = 'intent'
      // let e = { name, intent }
      // this.$emit('action', e) 
      let vo = { intents: [intent] }
      this.$store.dispatch('intent/run', vo)
    },

    handleClickItemMore() {
      // this.$emit("action", { name: "open-item-more" });
    },

    getSimpleRepository() {
      let str = JSON.stringify(this.repository)
      let r2 = JSON.parse(str)
      r2.projects = []
      r2.submodules = []
      r2.worktrees = []
      return r2;
    }

  },

  props: {
    repository: Object,
  },
};
</script>

