import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },

  // at home
  { path: "/repositories", component: () => import('../views/HomeLocalRepositories.vue') },
  { path: "/repositories/:repository_id", component: () => import('../views/LocalRepositoryOverview.vue') },
  { path: "/repositories/:repository_id/branches", component: () => import('../views/LocalRepositoryBranches.vue') },
  { path: "/repositories/:repository_id/commits", component: () => import('../views/LocalRepositoryCommits.vue') },
  { path: "/repositories/:repository_id/commits/:commit_id", component: () => import('../views/LocalRepositoryCommits.vue') },
  { path: "/repositories/:repository_id/projects", component: () => import('../views/LocalRepositoryProjects.vue') },
  { path: "/repositories/:repository_id/projects/:project_id", component: () => import('../views/LocalRepositoryProjects.vue') },
  { path: "/repositories/:repository_id/remotes", component: () => import('../views/LocalRepositoryRemotes.vue') },
  { path: "/repositories/:repository_id/tags", component: () => import('../views/LocalRepositoryTags.vue') },
  { path: "/repositories/:repository_id/tags/:tag_id", component: () => import('../views/LocalRepositoryTagDetail.vue') },
  { path: "/repositories/:repository_id/worktrees", component: () => import('../views/LocalRepositoryWorktrees.vue') },
  { path: "/repositories/:repository_id/worktrees/:worktree_id", component: () => import('../views/LocalRepositoryWorktrees.vue') },

  { path: "/remotes", component: () => import('../views/HomeRemoteRepositories.vue') },
  { path: "/executables", component: () => import('../views/HomeExecutables.vue') },
  { path: "/projects", component: () => import('../views/HomeProjects.vue') },

  { path: "/init/setup-or-import", component: () => import('@/components/init/setup-or-import') },

  // import
  { path: "/import-local-repo", component: () => import('../views/ImportLocalRepo') },
  { path: "/import-local-repository", component: () => import('../views/ImportLocalRepository') },
  { path: "/import-local-repositories", component: () => import('../views/ImportLocalRepositories') },

  // settings
  { path: "/settings", component: () => import('../views/Settings.vue') },
  { path: "/settings/backups", component: () => import('../views/SettingsBackups.vue') },
  { path: "/settings/dev", component: () => import('../views/SettingsDevelop.vue') },
  { path: "/settings/projects", component: () => import('../views/SettingsProjects.vue') },
  { path: "/settings/plug-ins", component: () => import('../views/SettingsPlugins.vue') },
  { path: "/settings/repositories", component: () => import('../views/SettingsLocalRepositories.vue') },
  { path: "/settings/remotes", component: () => import('../views/SettingsRemoteRepositories.vue') },
  { path: "/settings/executables", component: () => import('../views/SettingsExecutables') },
  { path: "/settings/intents", component: () => import('../views/IntentView.vue') },
  { path: "/settings/intent-templates", component: () => import('@/views/SettingsIntentTemplates') },
  { path: "/settings/images", component: () => import('@/views/SettingsImages.vue') },
  { path: "/settings/media", component: () => import('@/views/SettingsMediae.vue') },
  { path: "/settings/content-types", component: () => import('@/views/SettingsContentTypes.vue') },
  { path: "/settings/json-buffer", component: () => import('@/views/SettingsJsonBuffer.vue') },

  { path: "/setup", component: () => import('../views/Setup.vue') },

  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]


function prepareRoutes(list) {
  for (var i in list) {
    let item = list[i]
    let name = item.name
    if (name == null) {
      item.name = item.path;
    }
  }
}

prepareRoutes(routes)


const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
