<template>
  <div :style="myRootStyle">
    <el-table :data="items" ref="theTable" highlight-current-row :max-height="myTableMaxHeight"
      @selection-change="handleSelectionChange" @current-change="handleCurrentChange">
      <el-table-column :type="tableColumnType"></el-table-column>

      <el-table-column prop="name" label="" sortable>
        <template #default="scope">
          <el-button link type="primary" :title="scope.row.name" @click="handleClickItemName(scope.row)">
            {{ scope.row.name }}
          </el-button>
        </template>
      </el-table-column>

      <el-table-column prop="content_type" label="" sortable></el-table-column>

      <el-table-column prop="updated_at" label="" :formatter="formatTimestamp" sortable></el-table-column>

      <el-table-column prop="size" label="" :formatter="formatByteSize" sortable></el-table-column>
    </el-table>
  </div>
</template>

<script>
import TimeJS from "@/js/time.js";
import ByteJS from "@/js/bytes.js";

function nop() { }

export default {
  name: "filebrowser-view-detail-table",

  computed: {
    items() {
      let list = this.$store.getters["files/GetItems"](this.bank);
      if (this.displayAllItems) { return list }
      list = this.filterHiddenItems(list)
      return list;
    },

    tableColumnType() {
      return this.multipleSelection ? "selection" : "index";
      // return "selection";
    },

    myTableMaxHeight() {
      const max = 1024
      let h = this.wantHeight - 320;
      if (h > max) { h = max }
      return h + "px"
    },

    myRootStyle() {
      let minHeight = this.myTableMaxHeight;
      return { minHeight }
    },
  },

  data() {
    return {
      selection1: {},
      selectionN: [],
      // wantHeight: 0,
    };
  },

  methods: {
    handleClickItemName(item) {
      if (item.is_dir) {
        this.openDir(item);
      }
    },

    openDir(item) {
      let bank = this.bank;
      let href = item.name;
      this.$store.dispatch("files/fetch", { bank, href });
    },

    getFieldByName(name, item) {
      if (name == null || item == null) {
        return "";
      }
      return item[name];
    },

    handleCurrentChange(currentRow /* , oldCurrentRow */) {
      // let n2 = this.getFieldByName("name", currentRow);
      // let n1 = this.getFieldByName("name", oldCurrentRow);
      // console.warn("current-change old:" + n1 + " current:" + n2);
      ////////
      if (currentRow != null) {
        this.selection1 = currentRow;
      }
      this.fireSelectionChange();
    },

    handleSelectionChange() {
      let rows = this.$refs.theTable.getSelectionRows();
      // let cnt = rows.length;
      // console.warn("selection.count=" + cnt);
      ////////
      if (rows != null) {
        this.selectionN = rows;
      }
      this.fireSelectionChange();
    },

    fireSelectionChange() {
      let items = this.selectionN;
      let item1 = this.selection1;
      let dst = [];
      if (items != null) {
        for (var i in items) {
          dst.push(items[i]);
        }
      }
      if (dst.length == 0 && item1 != null) {
        if (item1.name != null) {
          dst.push(item1);
        }
      }
      this.$emit("selection-change", dst);
    },

    filterHiddenItems(list1) {
      let list2 = []
      for (var i in list1) {
        let item = list1[i];
        let name = "" + item.name
        let at = name.indexOf('.');
        if (at != 0) {
          list2.push(item)
        }
      }
      return list2
    },

    formatByteSize(row, column, cellValue, index) {
      nop(row, column, index);
      return ByteJS.StringifySize(cellValue);
    },

    formatTimestamp(row, column, cellValue, index) {
      nop(row, column, index);
      return TimeJS.StringifyTimestamp(cellValue);
    },
  },

  props: {
    bank: String,
    multipleSelection: Boolean,
    displayAllItems: Boolean,
    wantHeight: Number,
  },

  watch: {
  }
};
</script>

<style scoped></style>
