
const use_root = { root: true }


const state = {
    items: []
};

const getters = {
    list(state) {
        return state.items
    }
}

const mutations = {
    handle_fetch_resp_vo(state, vo) {
        if (vo == null) { return }
        let items = vo.items
        if (items == null) { return }
        state.items = items
    },

    handle_post_resp_vo(state, vo) {
        if (vo != null) {
            state.ok = true;
        }
    },
};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = 'init'
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_fetch_resp_vo', vo)
                context.commit('options/handle_fetch_resp_vo', vo.Option, use_root)
                return Promise.resolve(r)
            })
    },

    post(context) {
        let method = 'POST'
        let type = 'init'
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_post_resp_vo', vo)
                return Promise.resolve(r)
            })
    },
};

////////////////////////////////////////////////////////////////////////////////

export default {
    name: "module_init",
    namespaced: true,
    state, mutations, actions, getters
}
