
// import StateJS from './../js/state.js'


const use_root = { root: true }
const this_module_api_type = "packages"


////////////////////////////////////////////////////////////////////////////////

const state = {
    items: []
};

// const filter_obj = new Filter();
// const state_rw = StateJS.createStateRW({ pk: "id", listname: "executables" })
// state_rw.init(state)
// state_rw.setFilter(state, { filter: filter_obj })
// state_rw.setPage(state, { limit: 10, offset: 0 })


const getters = {
    list(state) {
        return state.items
    }
}

const mutations = {
    handle_fetch_resp_vo(state, vo) {
        if (vo == null) { return }
        let items = vo.items
        if (items == null) { return }
        state.items = items
    },
};


const actions = {
    fetch(context) {
        let method = 'GET'
        let type = this_module_api_type
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_fetch_resp_vo', vo)
                return Promise.resolve(r)
            })
    },

    update_sources(context) {
        let method = 'POST'
        let type = this_module_api_type
        let id = "update"
        let data = {}
        return context.dispatch("rest/execute", { method, type, id, data }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_update_resp_vo', vo)
                context.dispatch('refresh_pack_set_list')
                return Promise.resolve(r)
            })
    },

    refresh_pack_set_list(context) {
        return context.dispatch("soft_set/fetch", {}, use_root)
    },

};

////////////////////////////////////////////////////////////////////////////////

export default {
    name: "module_plugin_pack",
    namespaced: true,
    state, mutations, actions, getters
}
