
const use_root = { root: true }

////////////////////////////////////////////////////////////////////////////////

const state = {
};

const getters = {
}

const mutations = {
};

const actions = {
    copy(context, text) {
        navigator.clipboard
            .writeText(text)
            .then((r) => {

                if (text == 'test:error') {
                    return Promise.reject("text:err")
                }

                context.dispatch("display_copy_result", true);
                return Promise.resolve(r)
            })
            .catch((r) => {
                context.dispatch("display_copy_result", false);
                return Promise.reject(r)
            });
    },

    display_copy_result(context, ok) {
        let item = {
            level: 'info',
            type: "success", // [error|warn|success|default]
            action: "insert", // [fetch|insert|update|remove]
            message: "",
            display: true,
            source: "module_clipboard",
            duration: 3000,
        }
        if (!ok) {
            item.message = ""
            item.type = "error"
            item.level = "error"
        }
        context.commit('log/push', item, use_root)
    },
};

////////////////////////////////////////////////////////////////////////////////

export default {
    name: "module_clipboard",
    namespaced: true,
    state, mutations, actions, getters
}
