<template>
  <div>
    <el-dialog class="my-dialog" :modelValue="modelValue" @update:modelValue="updateModelValue" :fullscreen="fullscreen"
      :title="dialogTitle" @open="handleDialogOpen" align-center width="960px">
      <MyPathBar :bank="bank" />

      <MyDetailView class="my-detail-view" :bank="bank" :want-height="maxDialogHeight - 100"
        :display-all-items="displayAllItems" @selection-change="handleSelectionChange" />

      <div class="toolbar toolbar-top">

        <!-- <el-button-group > -->
        <el-button @click="handleClickGoUp" title="">
          <el-icon>
            <TopLeft />
          </el-icon>
        </el-button>

        <el-button @click="handleClickMakeDir" title="">
          <el-icon>
            <FolderAdd />
          </el-icon>
        </el-button>

        <el-button @click="handleClickRefresh" title="">
          <el-icon>
            <Refresh />
          </el-icon> </el-button>

        <!-- </el-button-group> -->

        <el-checkbox class="btn-hide-or-show-hidden-objects" v-model="displayAllItems"> 
        </el-checkbox>

        <el-input class="input-file-name" v-model="filename" placeholder="()"></el-input>

        <el-button type="default" @click="handleClickCancel"></el-button>

        <el-button type="success" @click="handleClickOK">
          {{ actionText }}
        </el-button>
      </div>

      <div class="toolbar toolbar-bottom"></div>

    </el-dialog>
  </div>
</template>

<script setup>
import { Refresh, FolderAdd, TopLeft } from "@element-plus/icons-vue";
</script>

<script>
import MyPathBar from "./path-bar.vue";
import MyDetailView from "./view-detail-table.vue";
import { ElMessageBox } from "element-plus";

export default {
  name: "filebrowser-dialog-common",

  components: { MyPathBar, MyDetailView },

  computed: {
    dialogTitle() {
      let title = this.title;
      if (title != null) {
        if (title != "") {
          return title;
        }
      }
      let s1 = this.actionText;
      let s2 = this.wantText;
      return s1 + s2;
    },

    actionText() {
      let value = this.action;
      return value == "save" ? "" : "";
    },

    wantText() {
      let value = this.want;
      return value == "folder" || value == "directory" ? "" : "";
    },
  },

  data() {
    return {
      filename: "",
      selectedItems: [],
      displayAllItems: false,
      maxDialogHeight: 100,
    };
  },

  methods: {
    refresh() {
      let bank = this.bank;
      this.$store.dispatch("files/fetch", { bank });
    },

    updateModelValue(p) {
      this.$emit("update:modelValue", p);
    },

    handleClickRefresh() {
      this.refresh();
    },

    handleClickGoUp() {
      let bank = this.bank;
      let href = "..";
      this.$store.dispatch("files/fetch", { bank, href });
    },

    handleClickMakeDir() {
      let title = "";
      let text = "";
      ElMessageBox.prompt(text, title, {
        confirmButtonText: "",
        cancelButtonText: "",
      })
        .then(() => {
          console.warn("todo: mkdir");
        })
        .catch(() => { });
    },

    handleClickCancel() {
      this.updateModelValue(false);
    },

    handleClickOK() {
      this.updateModelValue(false);
      let list = this.selectedItems;
      if (this.action == "save") {
        this.$emit("save-file", list);
      } else {
        this.$emit("open-file", list);
      }
    },

    handleSelectionChange(items) {
      if (items == null) {
        return;
      }
      if (items.length == 1) {
        this.filename = items[0].name;
        this.selectedItems = items;
        return;
      }
      let sep = "";
      let str = "";
      for (var i in items) {
        let it = items[i];
        str += sep + "'" + it.name + "'";
        sep = ", ";
      }
      this.selectedItems = items;
      this.filename = str;
    },

    init() {
      let path = "~";
      let base = "file:///";
      let bank = this.bank;
      this.$store.commit("files/init", { bank, path, base });
    },

    handleDialogOpen() {
      this.maxDialogHeight = window.innerHeight
    }
  },

  mounted() {
    this.init();
  },

  props: {
    modelValue: Boolean,
    bank: String,
    title: String,
    want: String, // ['file'|'folder']
    action: String, // ['open'|'save']
    multipleSelection: Boolean,
    fullscreen: Boolean,
  },

  watch: {
    modelValue(newValue, oldValue) {
      if (newValue == true && oldValue == false) {
        this.refresh();
      }
    },
  },
};
</script>

