.toolbar {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  margin-bottom: 10px;
}

.input-file-name {
  flex-grow: 1;
  margin-left: 10px;
  margin-right: 10px;
}

.my-detail-view {
  border-color: gray;
  border-width: 1px;
  border-style: solid;
  margin-top: 20px;
  margin-bottom: 20px;
  /* min-height: 256px; */
}

.toolbar-debug {
  background-color: yellow;
  border-color: black;
  border-width: 3px;
  border-style: solid;
}

.btn-hide-or-show-hidden-objects {
  margin-left: 10px;
  margin-right: 10px;
}
</style>
