<template>
  <HomeFrame :config="config">
    <div class="repo-frame-client">
      <h1 class="repo-name">{{ repository_name }}</h1>

      <div class="repo-description">{{ repository_description }}</div>
      <div class="repo-path">@ {{ repository_path }}</div>

      <el-tabs
        :modelValue="activeName"
        class="demo-tabs"
        @tab-change="handleChange"
      >
        <el-tab-pane label="" name="overview"> </el-tab-pane>
        <el-tab-pane label="" name="branches"> </el-tab-pane>
        <el-tab-pane label="" name="commits"> </el-tab-pane>
        <el-tab-pane label="" name="tags"> </el-tab-pane>
        <el-tab-pane label="" name="remotes"> </el-tab-pane>
        <el-tab-pane label="" name="worktrees"> </el-tab-pane>
        <el-tab-pane label="" name="projects"> </el-tab-pane>
      </el-tabs>

      <slot> </slot>
    </div>
  </HomeFrame>
</template>
  
<script>
import RouteJS from "./../../js/route";

const routejs = RouteJS.NewRouteJS();

function makeConfig() {
  return {
    pager: false,
    home: true,
    nav: true,
    search: false,
    searchCategory: "",
  };
}

export default {
  name: "local-repository-frame",

  computed: {
    repositoryid() {
      return routejs.GetRepositoryID(this);
    },

    repository_info() {
      return this.$store.getters["current_repository/info"];
    },

    repository_name() {
      return this.repository_info.name;
    },

    repository_path() {
      return this.repository_info.path;
    },

    repository_description() {
      return this.repository_info.description;
    },

    activeName() {
      return this.computeCurrentActiveName();
    },
  },

  data() {
    let config = makeConfig();
    return {
      config,
    };
  },

  methods: {
    computeCurrentActiveName() {
      let path = this.$route.path;
      let parts = path.split("/");
      let key = parts[3];
      if (key == null || key == "" || key == "overview") {
        return "overview";
      }
      return key;
    },

    fetch() {
      let id = this.repositoryid;
      this.$store.dispatch("current_repository/fetch", { id });
    },

    handleChange(name) {
      // console.log("tab-click: " + name);
      let view_name = "";
      if (name != "overview") {
        view_name = "/" + name;
      }
      let repo_id = this.repositoryid;
      let to = "/repositories/" + repo_id + view_name;
      this.$router.push(to);
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>
  
<style scoped>
.repo-frame-client {
  text-align: left;
  padding: 20px;
}

.repo-description {
  color: gray;
}

.repo-path {
  color: gray;
  margin-top: 10px;
  margin-bottom: 30px;
}
</style>
