

function stringifyNumWithWidth(num, width) {
    let str = num + ''
    if (num < 0) {
        return str
    }
    if (width == null) {
        return str
    }
    if (width < 2) {
        return str
    }
    for (; str.length < width;) {
        str = '0' + str
    }
    return str
}


////////////////////////////////////////////////////////////////////////////////
// public

/**
  ms 1970-01-01
  fmt  ["date"|"time"|"datetime"] 
*/
function StringifyTimestamp(ms, fmt) {
    if (ms == null) { ms = 0 }
    let t = new Date()
    t.setTime(ms)
    return StringifyDate(t, fmt)
}

/**
  date  javascript Date 
  fmt  ["date"|"time"|"datetime"] 
*/
function StringifyDate(date, fmt) {

    let t = date;
    if (t == null) {
        t = new Date();
        t.setTime(0)
    }

    let YY = t.getFullYear()
    let MM = t.getMonth() + 1
    let DD = t.getDate()
    let hh = t.getHours()
    let mm = t.getMinutes()
    let ss = t.getSeconds()

    let sep = ''
    let ymd = ''
    let hms = ''
    let has_ymd = false
    let has_hms = false

    if (fmt == "date") {
        has_ymd = true
    } else if (fmt == "time") {
        has_hms = true
    } else {
        // date-and-time
        has_ymd = true
        has_hms = true
        sep = "  "
    }

    if (has_ymd) {
        let s1 = '' + YY
        let s2 = stringifyNumWithWidth(MM, 2)
        let s3 = stringifyNumWithWidth(DD, 2)
        ymd = s1 + '-' + s2 + '-' + s3
    }

    if (has_hms) {
        let s1 = stringifyNumWithWidth(hh, 2)
        let s2 = stringifyNumWithWidth(mm, 2)
        let s3 = stringifyNumWithWidth(ss, 2)
        hms = s1 + ':' + s2 + ':' + s3
    }

    return ymd + sep + hms
}

////////////////////////////////////////////////////////////////////////////////

export default {
    name: "TimeJS",
    StringifyTimestamp,
    StringifyDate,
}
