

const use_root = { root: true }


const debug = 1
const preview = 2
const release = 3


const profile_map = {
    "": preview,

    "debug": debug,
    "dev": debug,
    "dev-windows": debug,
    "dev-linux": debug,

    "preview": preview,

    "release": release,
}



const state = {
    profiles: [debug, preview, release],
    active: preview,
};

const getters = {
    debug(state) {
        return (state.active <= debug)
    },

    preview(state) {
        return (state.active <= preview)
    },

    release(state) {
        return (state.active <= release)
    }
}

const mutations = {
    handle_vo(state, vo) {
        let p1 = vo.profile;
        let p2 = profile_map[p1]
        if (p2 == null) {
            p2 = preview;
        }
        state.active = p2;
    }
};

const actions = {

    fetch(context) {
        let method = 'GET'
        let type = 'about'
        let id = 'debug-mode'
        return context.dispatch('rest/execute', { method, type, id }, use_root).then((r) => {
            let vo = r.data;
            context.commit('handle_vo', vo)
            return Promise.resolve(r)
        })
    }

};


export default {
    name: "module_develop",
    namespaced: true,
    state, mutations, actions, getters
}
