      :style="getListItemStyle(item)"
      v-for="item in data"
      :key="item.id"
    >
      <MyItem
        class="list-item-inner"
        :data="item"
        @action="handleAction"
        :expanded="item.id == expandedItemId"
      />
    </div>
  </div>
