<script>
import MyItem from "./item.vue";

export default {
  name: "home-local-repository-repos",
  components: { MyItem },

  methods: {
    handleAction(e) {
      this.$emit("action", e);
    },

    getListItemStyle(item) {
      let exp = item.id == this.expandedItemId;
      if (exp) {
        return {
          "background-color": "rgb(245,245,245)",
        };
      }
      return {};
    },

    handleClickOpenItemWith(item) {
      let name = "open-item-with";
      let e = { name, item };
      this.$emit("action", e);
    },

    // handleClickItemBg(item) {
