

const use_root = { root: true }
const this_module_api_type = "options"


function isEnabled(o, name) {
    if (o == null || name == null) {
        return false
    }
    let value = o[name]
    return (value == true || value == 1)
}


////////////////////////////////////////////////////////////////////////////////

const state = {
    options: {}
};

const getters = {
    debug(state) {
        return isEnabled(state.options, "wpm.options.debug")
    }
}


// handle_fetch_resp_vo
const mutations = {
    handle_fetch_resp_vo(state, vo) {
        state.options = vo.options
    },
};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = this_module_api_type
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_fetch_resp_vo', vo)
                return Promise.resolve(r)
            })
    },
};

////////////////////////////////////////////////////////////////////////////////

export default {
    name: "module_options",
    namespaced: true,
    state, mutations, actions, getters
}
