

const use_root = { root: true }


const state = {
    items: []
};

const getters = {
    list(state) {
        return state.items
    }
}

const mutations = {

    handle_vo(state, vo) {
        let list = vo.backups;
        if (list != null) {
            state.items = list
        }
    }

};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = 'backups'
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_vo', vo)
                return Promise.resolve(r)
            })
    },
};


export default {
    name: "module_backup",
    namespaced: true,
    state, mutations, actions, getters
}
