
import Getter from './getter.js'


////////////////////////////////////////////////////////////////////////////////


function IntentInfoLoader() {
    this.info = {};
    this.intent = {};
}

IntentInfoLoader.prototype = {

    load(intent) {
        if (intent == null) { return }
        this.tryLoadFile(intent.file);
        this.tryLoadFolder(intent.folder);
        this.tryLoadProject(intent.project);
        this.tryLoadRepository(intent.repository);
        this.tryLoadSubmodule(intent.submodule);
        this.tryLoadWeb(intent.web);
        this.tryLoadWorktree(intent.worktree);
        return this.info;
    },

    tryLoadFolder(item) {
        if (item == null) {
            return;
        }
        let info = {}
        info.typetext = "";
        info.type = "folder";
        info.name = item.name;
        info.path = item.path;
        this.info = info;
        this.intent = { folder: item };
    },

    tryLoadFile(item) {
        if (item == null) {
            return;
        }
        let info = {}
        info.typetext = "";
        info.type = "file";
        info.name = item.name;
        info.path = item.path;
        this.info = info;
        this.intent = { file: item };
    },

    tryLoadWeb(item) {
        if (item == null) {
            return;
        }
        let info = {}
        info.typetext = "";
        info.type = "web";
        info.name = item.name;
        info.location = item.location;
        this.info = info;
        this.intent = { file: item };
    },

    tryLoadProject(item) {
        if (item == null) {
            return;
        }
        let info = {}
        info.typetext = "";
        info.type = "project";
        info.name = item.name;
        info.path = item.path;
        this.info = info;
        this.intent = { project: item };
    },

    tryLoadRepository(item) {
        if (item == null) {
            return;
        }
        let info = {}
        info.typetext = "";
        info.type = "repository";
        info.name = item.name;
        info.path = item.path;
        this.info = info;
        this.intent = { repository: item };
    },

    tryLoadSubmodule(item) {
        if (item == null) {
            return;
        }
        let info = {}
        info.typetext = "";
        info.type = "submodule";
        info.name = item.name;
        info.path = item.path;
        this.info = info;
        this.intent = { submodule: item };
    },

    tryLoadWorktree(item) {
        if (item == null) {
            return;
        }
        let info = {}
        info.typetext = "";
        info.type = "worktree";
        info.path = item.path;
        info.name = item.name;
        this.info = info;
        this.intent = { worktree: item };
    },

}


////////////////////////////////////////////////////////////////////////////////


function Intent() {

    // let loader = new IntentInfoLoader();
    // let i = loader.load(p)

    // this.myTargetClass = i.class;
    // this.myTargetName = i.name;
}


Intent.prototype = {

    GetTargetClass() {
        return this.myTargetClass;
    },

    GetTargetName() {
        return this.myTargetName;
    },

}


////////////////////////////////////////////////////////////////////////////////

function IntentWrapper(intent) {
    if (intent == null) {
        intent = {}
    }

    let loader = new IntentInfoLoader()
    let info = loader.load(intent)

    this.path = Getter.Get(info, "path", "");
    this.type = Getter.Get(info, "type", "");
    this.tt = Getter.Get(info, "typetext", "");
    this.name = Getter.Get(info, "name", "[]");
    this.intent = intent;
}


IntentWrapper.prototype = {

    GetType() {
        return this.type
    },

    GetTypeText() {
        return this.tt
    },

    GetName() {
        return this.name
    },

    GetPath() {
        return this.path
    },

}



////////////////////////////////////////////////////////////////////////////////


export default {
    name: "Intent",

    Wrap(p) {
        return new IntentWrapper(p)
    },

    NewIntent() {
        return new Intent()
    },
}
