const use_root = { root: true }
const this_module_api_type = "jsonbuffer"


////////////////////////////////////////////////////////////////////////////////

const state = {
    vo: {
        contenttypes: [],
        intenttemplates: [],
        mediae: [],
        executables: [],
    }
};

const the_empty_vo_template = JSON.stringify(state.vo)

// const filter_obj = new Filter();
