// state_rw.init(state)
// state_rw.setFilter(state, { filter: filter_obj })
// state_rw.setPage(state, { limit: 10, offset: 0 })


const getters = {
    vo(state) {
        return state.vo
    }
}

const mutations = {

    handle_fetch_resp_vo(state, vo) {
        if (vo == null) { return }
        state.vo = vo
    },

    // reset(state) {
    //     state.vo = JSON.parse(the_empty_vo_template)
    // },

    // put(state, p) {
    //     let type = p.type;
    //     let srclist = p.items;
    //     let dstlist = state.vo[type];
    //     if (srclist == null || dstlist == null) { return; }
    //     for (var i in srclist) {
    //         let item = srclist[i]
    //         dstlist.push(item)
    //     }
    // }

};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = this_module_api_type
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_fetch_resp_vo', vo)
                return Promise.resolve(r)
            })
    },

    reset(context) {
        let method = 'POST'
        let type = this_module_api_type
        let id = 'reset'
        let data = {}
        return context.dispatch("rest/execute", { method, type, id, data }, use_root)
            .then((r) => {
                context.dispatch('fetch')
                return Promise.resolve(r)
            })
    },

    put(context, p) {
        let method = 'PUT'
        let type = this_module_api_type
        let data = p.vo;
        if (data == null) { data = {} }
        return context.dispatch("rest/execute", { method, type, data }, use_root)
            .then((r) => {
                context.dispatch('fetch')
                return Promise.resolve(r)
            })
    },

};

////////////////////////////////////////////////////////////////////////////////

export default {
    name: "module_json_buffer",
    namespaced: true,
    state, mutations, actions, getters
}
