<template>
  <div></div>
</template>

<script>
import { ElMessage, ElMessageBox, ElNotification } from "element-plus";

const log_level_num = {
  trace: 1,
  debug: 2,
  info: 3,
  warn: 4,
  error: 5,
  fatal: 6,
};

const log_level_text = {
  trace: "",
  debug: "",
  info: "",
  warn: "",
  error: "",
  fatal: "",
};

export default {
  name: "utils-log-viewer",

  computed: {
    logs() {
      return this.$store.getters["log/logs"];
    },

    todo() {
      return this.$store.getters["log/todo"];
    },

    revision() {
      return this.$store.getters["log/revision"];
    },
  },

  methods: {
    displayLogItem(item) {
      if (item == null) {
        return;
      }
      if (item.read) {
        return;
      }
      if (!item.display) {
        return;
      }
      item.read = true;
      ////
      let action = item.action;
      let ok = this.isok(item);
      if (action == "fetch") {
        if (ok) {
          // nop
        } else {
          this.displayMessageBalloon(item);
        }
      } else {
        if (ok) {
          this.displayMessageTip(item);
        } else {
          this.displayMessageDialog(item);
        }
      }
    },

    isok(item) {
      let level = this.getLogLevelNum(item);
      return level <= log_level_num.info;
    },

    displayMessageTip(item) {
      let message = item.message;
      let type = item.type;
      let duration = item.duration;
      let showClose = true;
      ElMessage({ message, type, duration, showClose });
    },

    displayMessageBalloon(item) {
      let message = item.message;
      let type = item.type;
      let duration = item.duration;
      let showClose = true;
      let title = this.getLogLevelText(item);
      ElNotification({ message, title, type, duration, showClose });
    },

    displayMessageDialog(item) {
      let message = item.message;
      let type = item.type;
      let title = this.getLogLevelText(item);
      // let duration = item.duration;
      // let showClose = true;
      ElMessageBox.alert(message, title, { type });
    },

    getLogLevelText(item) {
      let key = item.level;
      let text = log_level_text[key];
      if (text == null) {
        text = key;
      }
      if (text == null) {
        text = "no_level";
      }
      return text;
    },

    getLogLevelNum(item) {
      let key = item.level;
      let num = log_level_num[key];
      if (num == null) {
        return 0;
      }
      return num;
    },

    displayLogs() {
      let count = 0;
      let items = this.todo;
      if (items != null) {
        for (var i in items) {
          let item = items[i];
          this.displayLogItem(item);
          count++;
        }
      }
      if (count > 5) {
        return this.$store.commit("log/clean");
      }
    },
  },

  watch: {
    revision() {
      this.displayLogs();
    },
  },
};
</script>
