
import PathJS from './path.js'


const Fields = {
    name: "name",
    bank: "bank",

    path: "path",
    baseurl: "base",
    items: "items",
    self: "self",
}


///////////////////////////////////////////////////////////////////////////////

function StateBank(state, name) {

    let d = state[name]
    if (d == null) {
        d = {}
    }

    this.name = name;
    this.state = state;
    this.data = d;
}

StateBank.prototype = {

    _getValueByName(name, def) {
        let value = this.data[name];
        if (value == null) {
            value = def;
        }
        return value;
    },

    _copy_fields(src, dst, callback) {
        if (src == null || dst == null) {
            return;
        }
        for (var name in src) {
            let pold = dst[name]
            let pnew = src[name]
            if (callback != null) {
                callback(name, pold, pnew)
            }
            dst[name] = pnew
        }
    },

    Init(p) {
        let dst = this.data;
        let src = p;
        const h = (name, v1, v2) => {
            if (v1 == null) {
                console.warn("StateBank.Init a bad field, name:[" + name + "] v1:[" + v1 + "] v2:[" + v2 + "]")
            }
        }
        this._copy_fields(src, dst, h)
    },

    GetSelf() {
        return this._getValueByName(Fields.self, {});
    },

    GetItems() {
        return this._getValueByName(Fields.items, []);
    },

    GetPathString() {
        return this._getValueByName(Fields.path, "");
    },

    GetBaseURL() {
        return this._getValueByName(Fields.baseurl, "");
    },

    GetPathElements() {
        let str = this.GetPathString();
        let els = PathJS.ToElements(str)
        return PathJS.NormalizeElements(els)
    },

    UpdateVO(vo) {
        let ext = vo.self.exists
        if (!ext) {
            return;
        }
        let src = vo;
        let dst = this.data;
        this._copy_fields(src, dst);
    },

}


///////////////////////////////////////////////////////////////////////////////

function StateRW() {
}

StateRW.prototype = {

    createBank(name) {
        let b = {}
        b[Fields.name] = name;
        b[Fields.bank] = name;
        b[Fields.baseurl] = "";
        b[Fields.path] = "";
        return b;
    },

    InitWithBanks(state, bank_name_list) {
        for (var i in bank_name_list) {
            let name = bank_name_list[i]
            state[name] = this.createBank(name)
        }
    },

    GetBank(state, bank) {
        return new StateBank(state, bank)
    }
}

///////////////////////////////////////////////////////////////////////////////

function CreateStateRW() {
    return new StateRW()
}

export default {
    name: "file_browser",
    CreateStateRW
}
