
import StateJS from './../js/state.js'

const use_root = { root: true }

////////////////////////////////////////////////////////////////////////////////

function Filter() {
    this.text = ''
}

Filter.prototype = {

    containString(keyword, text) {
        if ((keyword == null) || (text == null)) { return false }

        keyword = this.normalizeText(keyword)
        text = this.normalizeText(text)

        let i = text.indexOf(keyword)
        return (i >= 0)
    },

    normalizeText(text) {
        if (text == "") { return "" }
        return text.trim().toLowerCase()
    },

    prepare(text) {
        this.text = text;
    },

    accept(item) {
        let filter = this.text;
        if (this.containString(filter, item.name)) { return true }
        if (this.containString(filter, item.title)) { return true }
        if (this.containString(filter, item.description)) { return true }
        // if (this.containString(filter, item.path)) { return true }
        return false;
    },

}


////////////////////////////////////////////////////////////////////////////////


function FetchRequest(context, p) {
    if (p == null) {
        p = {}
    }
    this.context = context;
    this.ids = p.ids;
}

FetchRequest.prototype = {

    prepareFetchByIds(ids) {
        let p = {}
        p.ids = this.makeIDs(ids)
        return p
    },

    makeIDs(ids) {
        if (ids == null) { return null; }
        let count = 0;
        let str = ""
        for (var i in ids) {
            let id = ids[i];
            str += (id + ".")
            count++;
        }
        if (count < 1) { return null; }
        return str
    },
}


function getCurrentPageItemIds(context) {
    let state = context.state;
    let items = state_rw.listCurrentPage(state)
    let dst = []
    for (var i in items) {
        let id = items[i].id;
        dst.push(id)
    }
    return dst;
}


////////////////////////////////////////////////////////////////////////////////

const state = {
    projects: []
};

const filterObj = new Filter();
const state_rw = StateJS.createStateRW({ pk: "id", listname: "projects" })
state_rw.init(state)
state_rw.setFilter(state, { text: "", filter: filterObj })
state_rw.setPage(state, { limit: 10, offset: 0 })


const getters = {

    listAll(state) {
        return state_rw.listAll(state)
    },

    listAfterFilter(state) {
        return state_rw.listFilterResults(state)
    },

    listCurrentPage(state) {
        return state_rw.listCurrentPage(state)
    },

    filterText(state) {
        return state_rw.getFilterText(state)
    },

    currentPage(state) {
        return state_rw.currentPageNumber(state)
    },

    pageSize(state) {
        return state_rw.limit(state)
    },

    total(state) {
        return state_rw.total(state)
    },

    listCurrentPageIds(state) {
        return state_rw.listCurrentPageIds(state)
    },

    stringifyCurrentPageIds(state) {
        return state_rw.stringifyCurrentPageIds(state)
    },

}

const mutations = {

    handle_fetch_all(state, vo) {
        state_rw.reset(state)
        state_rw.updateVO(state, vo)
    },

    handle_fetch_by_ids(state, vo) {
        state_rw.updateVO(state, vo)
    },

    update_items(state, vo) {
        state_rw.updateVO(state, vo)
    },

    remove_item(state, item) {
        state_rw.remove(state, item.id)
    },

    setFilter(state, p) {
        state_rw.setFilter(state, p)
    },

    update(state, p) {

        let vo = p.vo;
        let limit = p.size;
        let index = p.index;
        let text = p.filter;

        if (vo != null) {
            state_rw.updateVO(state, vo)
        }

        if (limit != null || index != null) {
            state_rw.setPage(state, { limit, index })
        }

        if (text != null) {
            state_rw.setFilter(state, { text })
        }

        // let src = state_rw.listAll(state);
        // let dst = makeOpenWithList(src)
        // state.openWithList = dst
    },

    reset(state) {
        state_rw.reset(state)
    }
};

const actions = {

    fetch(context, p) {

        // pre-del

        // let all = p.all
        // let ids = p.ids
        // let params = {}
        // if (all) {
        //     params.all = 1
        // } else if (ids != null) {
        //     params.ids = ids
        // }

        // let method = 'GET'
        // let type = 'projects'
        // let p2 = { method, type, params };
        // return context.dispatch("rest/execute", p2, use_root).then((r) => {
        //     let vo = r.data;
        //     if (all) {
        //         context.commit('handle_fetch_all', vo)
        //     } else {
        //         context.commit('handle_fetch_by_ids', vo)
        //     }
        //     return Promise.resolve(r);
        // })

        return context.dispatch('fetchAll', p)
    },

    fetchAll(context) {
        let method = 'GET'
        let type = 'projects'
        let params = { all: 1 }
        let p = { method, type, params }
        return context.dispatch("rest/execute", p, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('reset')
                context.commit('update', { vo })
                context.dispatch('fetchByIds', {})
                return Promise.resolve(r)
            })
    },

    fetchByIds(context, { ids }) {

        if (ids == null) {
            ids = getCurrentPageItemIds(context)
        }

        let req = new FetchRequest(context)
        let params = req.prepareFetchByIds(ids)
        params['with-file-state'] = 1
        // params['with-projects'] = 1

        let method = 'GET'
        let type = 'projects'
        let p = { method, type, params }
        return context.dispatch("rest/execute", p, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('update', { vo })
                return Promise.resolve(r)
            })
    },

    insert(context, p) {
        let item = p.item;
        let method = 'POST'
        let type = 'projects'
        let data = {
            projects: [item]
        }
        let p2 = { method, type, data };
        return context.dispatch("rest/execute", p2, use_root).then((r) => {
            let vo = r.data;
            context.commit('update_items', vo)
            return Promise.resolve(r);
        })
    },

    update(context, p) {
        let item = p.item;
        let method = 'PUT'
        let type = 'projects'
        let id = item.id;
        let data = {
            projects: [item]
        }
        let p2 = { method, type, id, data };
        return context.dispatch("rest/execute", p2, use_root).then((r) => {
            let vo = r.data;
            context.commit('update_items', vo)
            return Promise.resolve(r);
        })
    },

    remove(context, item) {
        let method = "DELETE"
        let type = 'projects'
        let id = item.id;
        let p2 = { method, type, id };
        return context.dispatch("rest/execute", p2, use_root).then((r) => {
            // let vo = r.data;
            context.commit('remove_item', item)
            return Promise.resolve(r);
        })
    }

};



export default {
    name: "module_project",
    namespaced: true,
    state, mutations, actions, getters
}
